<template>
  <div id="DocMain" style="height: 100%;">
    <avue-crud
      ref="dataTable"
      :data="data"
      :option="option"
      :page="page"
      :table-loading="loading"
      :header-cell-class-name="handlemyclass"
      @row-click="rowClick"
      @size-change="sizeChange"
      @row-update="rowUpdate"
      @refresh-change="refreshChange"
      @current-change="currentChange"
      @selection-change="selectionChange">
        <template slot-scope="scope" slot="FILENUM">
          <span slot="reference" v-if="classType == '0' && scope.row['FILENUM'] != '' && scope.row['FILENUM'] > 0" title="原 件" style="background: rgb(245, 108, 108); border-radius: 15px; padding: 2px 7px; color: #fff; font-weight: 700; cursor: pointer;">
            {{scope.row['FILENUM']}}
          </span>
        </template>
        <template slot="menuLeft">
          <el-button type="primary" size="mini" v-if="haveAdd" @click.stop="btnAddClick" title="新增"><i class="el-icon-plus"/> 新 增</el-button>
          <el-button type="primary" size="mini" @click.stop="btnPrintClick" title="打印"><i class="el-icon-printer"/> 打 印</el-button>
          <el-button type="primary" size="mini" v-if="haveImport" @click.stop="btnImportClick" title="导入"><i class="el-icon-sold-out"/> 导 入</el-button>
          <el-button type="primary" size="mini" v-if="haveExport" @click.stop="btnExportClick" title="导出"><i class="el-icon-sold-out"/> 导 出</el-button>
          <el-button type="primary" size="mini" v-if="haveReplace" @click.stop="btnReplaceClick" title="批量替换"><i class="el-icon-refresh"/> 批量替换</el-button>
          <el-button type="primary" size="mini" v-if="haveDelete" @click.stop="btnDeleteManyClick" title="批量删除"><i class="el-icon-delete"/> 批量删除</el-button>
          <el-button type="primary" size="mini" @click.stop="btnSearchClick" title="查 询"><i class="el-icon-search"/> 查 询</el-button>
          <!-- <el-button type="primary" size="mini" @click.stop="btnEmptyClick" title="全部清空"><i class="el-icon-news"/> 全部清空</el-button> -->
        </template>
        <template slot-scope="scope" slot="menu" style="text-align: left">
          <i class="el-icon-delete" v-if="haveDelete" title="删 除" style="font-size: 18px; color: #f00; cursor: pointer; margin-right: 5px;" @click.stop="btnDelClick(scope.row, scope.index)"></i>
          <i class="el-icon-edit" v-if="haveEdit" title="修 改" style="font-size: 18px; color: #00f; cursor: pointer;margin-right: 5px;" @click.stop="btnEditClick(scope.row,scope.index)"></i>
          <i class="el-icon-search" title="查 看" style="font-size: 18px; color: #000; cursor: pointer;margin-right: 5px;" @click.stop="btnViewClick(scope.row,scope.index)"></i>
          <i class="el-icon-message" title="原件查看及操作" v-if="haveFileNum" style="font-size: 18px; color: #000; cursor: pointer;" @click.stop="btnMountClick(scope.row,scope.index)"></i>
          <!-- <i class="el-icon-message" title="查看原件" style="font-size: 18px; color: #000; cursor: pointer;" @click.stop="btnMountClick(scope.row,scope.index)"></i> -->
        <!-- <el-dropdown-item
          divided
          type="primary"
          icon="el-icon-check"
          size="mini"
          plain
          @click.native="editData(scope.row,scope.index)">编 辑</el-dropdown-item> -->
        <!-- <el-dropdown-item
          divided
          type="warning"
          icon="el-icon-check"
          size="mini"
          plain
          @click.native="delData(scope.row,scope.index)">删 除</el-dropdown-item> -->
      </template>
    </avue-crud>
    <doc-form @show="showEditor" ref="docForm" :adddatadesc="adddatadesc" :classId="classId" :classType="classType" @dataSave="dataSave"/>
    <ReplaceForm @show="showReplace" ref="replaceForm" :colArray="option.column" @replaceSave="replaceSave"></ReplaceForm>
    <el-popover
      popper-class="popperclass"
      ref="popover"
      title="原件列表"
      v-model="visible2"
      @show="popShow"
      >
      <files-view ref="fileView"></files-view>
      <el-button size="mini" type="text" @click="visible2 = false">关闭</el-button>
    </el-popover>
    <el-dialog
      ref="popoverPrinter"
      title="自定义报表"
      width="340px"
      :visible="visiblePrinte"
      :before-close="closePrinteList"
      >
      <div style="height: 150px">
        <el-scrollbar style="height: 100%" class="reportListView" :native="false" >
          <dl>
            <dd v-for="(report, index) in reports" :key="report.REPID" class="reportItem" v-bind:class="{even: index % 2 === 0}" @click.stop="btnPreviewClick(report, index)">
              <span style="color: #000;">{{report.REPNAME}}</span>
            </dd>
          </dl>
        </el-scrollbar>
      </div>
      <div slot="footer" style="text-align: center;">
        <el-button size="mini" type="warning" @click="closePrinteList(true)">关闭</el-button>
      </div>
    </el-dialog>
    <Exportxls ref="exportxls" @exportForm="exportForm"></Exportxls>
    <easy-star-import ref="importForm"/>
    <easy-star-mount ref="mountForm"/>
    <Search :searCols="cols" ref="searchForm" @searchForm="searchForm" @alldata="fetchData"></Search>
  </div>
</template>

<script>
import ArchiveHome from '@/components/ArchiveHome'
import EasyStarMenu from '@/components/docManager/nav/EasyStarMenu'
import FilesView from '@/components/fileManager/FilesView'
import DocForm from '@/components/docManager/doc/DocForm'
import ReplaceForm from '@/components/docManager/doc/ReplaceForm'
import Search from '@/components/docManager/doc/Search'
import Exportxls from '@/components/docManager/doc/Exportxls'
import EasyStarImport from '@/components/import/EasyStarImport'
import EasyStarMount from '@/components/mount/EasyStarMount'
export default {
  name: 'DocMain',
  components: {
    EasyStarMenu: EasyStarMenu,
    FilesView: FilesView,
    ArchiveHome: ArchiveHome,
    DocForm: DocForm,
    ReplaceForm: ReplaceForm,
    Search: Search,
    Exportxls: Exportxls,
    EasyStarImport: EasyStarImport,
    EasyStarMount: EasyStarMount
  },
  data () {
    return {
      showEditor: false,
      showReplace: false,
      haveAdd: false,
      haveImport: false,
      haveExport: false,
      haveReplace: false,
      haveDelete: false,
      haveEdit: false,
      haveFileNum: false,
      cols: [],
      dicts: {
        SAVEDATE: [
          {
            DICTID: 'Y',
            DVALUE: '永久'
          },
          {
            DICTID: 'D',
            DVALUE: '短期'
          },
          {
            DICTID: 'C',
            DVALUE: '长期'
          }
        ],
        SECURITY: [
          {
            DICTID: '01',
            DVALUE: '秘密'
          },
          {
            DICTID: '02',
            DVALUE: '机密'
          }
        ]
      },
      currentIndex: -1,
      currentDocId: '',
      reports: [],
      // reports: [
      //   {
      //     id: '1',
      //     name: '备考表'
      //   },
      //   {
      //     id: '2',
      //     name: '归档文件目录（横）'
      //   },
      //   {
      //     id: '3',
      //     name: '文件整理单'
      //   },
      //   {
      //     id: '4',
      //     name: '归档文件目录索引'
      //   }
      // ],
      // 显示自定义报表
      visiblePrinte: false,
      // 显示原件列表
      visible2: false,
      title: '档案管理',
      classId: '',
      classType: '',
      data: [],
      loading: false,
      page: {
        pageSizes: [20, 40, 80, 100],
        pageSize: 10,
        currentPage: 1,
        total: 0
      },
      option: {
        height: window.innerHeight - 230,
        expand: false,
        highlightCurrentRow: true,
        addBtn: false,
        editBtn: false,
        delBtn: false,
        border: true,
        stripe: true,
        showHeader: true,
        index: false,
        size: 'mini',
        selection: true,
        page: true,
        align: 'center',
        menu: true,
        menuAlign: 'center',
        menuType: 'icon',
        menuWidth: '120px',
        column: []
      },
      // 新增、修改成功提示
      adddatadesc: '',
      // 批量删除/批量替换/导出条目的数组
      manyparamsid: [],
      keyWords: [],
      operationArr: [] // 后台返回权限的数组
    }
  },
  mounted: function () {
    // document.getElementsByClassName('el-table__body-wrapper')[0].addEventListener('scroll', this.scroll, false)
    // this.dom.addEventListener('scroll', this.scroll, false)
    this.$nextTick(async function () {
      this.page.pageSize = this.page.pageSizes[0]
      await this.init()
    })
  },
  watch: {
    // 如果路由有变化，会再次执行该方法
    '$route': 'init'
  },
  methods: {
    btnSearchClick: function () { // 点击精确搜索
      this.$refs.searchForm.show(true)
      console.log('传递的列', this.cols)
    },
    init: async function () {
      this.classType = this.$route.params.classType
      this.operationArr = this.$route.params.arr
      if (typeof (this.classType) !== 'undefined' && this.classType !== '') {
        switch (this.classType) {
          case '0':
            this.operationArr.find((item) => {
              // console.log('item权限', item)
              switch (item.MENUID) {
                case 'MINFO000000000020102':
                  this.haveAdd = true
                  break
                case 'MINFO000000000020605':
                  this.haveImport = true
                  break
                case 'MINFO000000000020606':
                  this.haveExport = true
                  break
                case 'MINFO000000000020105':
                  this.haveReplace = true
                  break
                case 'MINFO000000000020104':
                  this.haveDelete = true
                  break
                case 'MINFO000000000020103':
                  this.haveEdit = true
                  break
              }
            })
            break
          case '1':
            this.operationArr.find((item) => {
              switch (item.MENUID) {
                case 'MINFO000000000020201':
                  this.haveAdd = true
                  break
                case 'MINFO000000000020605':
                  this.haveImport = true
                  break
                case 'MINFO000000000020606':
                  this.haveExport = true
                  break
                case 'MINFO000000000020210':
                  this.haveReplace = true
                  break
                case 'MINFO000000000020203':
                  this.haveDelete = true
                  break
                case 'MINFO000000000020202':
                  this.haveEdit = true
                  break
              }
            })
            break
          case '2':
            this.operationArr.find((item) => {
              switch (item.MENUID) {
                case 'MINFO000000000020301':
                  this.haveAdd = true
                  break
                case 'MINFO000000000020605':
                  this.haveImport = true
                  break
                case 'MINFO000000000020606':
                  this.haveExport = true
                  break
                case 'MINFO000000000020310':
                  this.haveReplace = true
                  break
                case 'MINFO000000000020303':
                  this.haveDelete = true
                  break
                case 'MINFO000000000020302':
                  this.haveEdit = true
                  break
              }
            })
            break
        }
      }
      this.fetchData()
    },
    handlemyclass: function (row, column, rowIndex, columnIndex) {
      return 'headerClass'
    },
    dataSave (isNew, cols, row) {
      let that = this
      this.adddatadesc = ''
      this.classType = this.$route.params.classType
      if (isNew) {
        // 如果是新增 (分为文件级、盒级、案卷级)
        if (typeof (this.classType) !== 'undefined' && this.classType !== '') {
          var addaction = ''
          switch (this.classType) {
            case '0':
              addaction = this.easyStarAPI.AddDocTable
              break
            case '1':
              addaction = this.easyStarAPI.AddBoxTable
              break
            case '2':
              addaction = this.easyStarAPI.AddRoolTable
              break
          }
          that.axios.post(that.api + addaction, {
            data: row
          }).then((res) => {
            if (res.data.code === 0) {
              // this.data.splice(0, 0, row)
              this.$refs['docForm'].innerVisible = true
              this.adddatadesc = '新增数据成功'
              this.fetchData()
            } else {
              // this.adddatadesc = res.data.errMsg
              this.$message({
                type: 'error',
                message: res.data.errMsg
              })
            }
          }).catch((error) => {
            console.log(error)
          })
        }
      } else {
        // 如果是修改
        if (typeof (this.classType) !== 'undefined' && this.classType !== '') {
          var updateaction = ''
          switch (this.classType) {
            case '0':
              updateaction = this.easyStarAPI.UpdateDocTable
              break
            case '1':
              updateaction = this.easyStarAPI.UpdateBoxTable
              break
            case '2':
              updateaction = this.easyStarAPI.UpdateRoolTable
              break
          }
          that.axios.put(that.api + updateaction, {
            data: row
          }).then((res) => {
            console.log('更新的结果', res)
            if (res.data.code === 0) {
              this.$refs['docForm'].innerVisible = true
              this.data.splice((row.$index), 1, row)
              this.adddatadesc = '数据修改成功'
            } else {
              // this.adddatadesc = res.data.errMsg
              this.$message({
                type: 'error',
                message: res.data.errMsg
              })
            }
          }).catch((error) => {
            console.log(error)
          })
        }
        // cols.forEach(element => {
        //   that.data[row.$index][element.FIELDNAME] = row[element.FIELDNAME]
        // })
      }
    },
    btnAddClick () {
      // console.log('classID', this.classId)
      // console.log('classType', this.classType)
      this.showEditor = true
      this.$refs['docForm'].title = '新 增'
      this.$refs['docForm'].partNum = ''
      this.$refs['docForm'].keyNum = ''
      this.$refs['docForm'].isErrorDoc = false // 档号错误提示
      this.$refs['docForm'].isNew = true
      this.$refs['docForm'].isView = false
      this.$refs['docForm'].createNewDoc()
      this.$refs['docForm'].visible = true
      this.$refs['docForm'].loadCols(this.cols)
      this.$refs['docForm'].loadData({})
      // this.$refs['docForm'].loadData({})
      // this.$refs['docForm'].dicts = this.dicts
      // this.$refs['docForm'].$emit('objDicts')
    },
    btnEditClick (row, index) {
      var params = ''
      // console.log('classID', this.classId)
      // console.log('classType', this.classType)
      if (typeof (this.classType) !== 'undefined' && this.classType !== '') {
        switch (this.classType) {
          case '0':
            params = row.DOCID
            break
          case '1':
            params = row.BOXID
            break
          case '2':
            params = row.ROOLID
            break
        }
      }
      this.$refs['docForm'].showParams(params)
      this.$refs.dataTable.setCurrentRow(row)
      this.showEditor = true
      this.$refs['docForm'].title = '修 改'
      this.$refs['docForm'].keyNum = ''
      this.$refs['docForm'].isErrorDoc = false // 档号错误提示
      this.$refs['docForm'].isNew = false
      this.$refs['docForm'].isView = false
      this.$refs['docForm'].visible = true
      this.$refs['docForm'].loadCols(this.cols)
      this.$refs['docForm'].loadData(row)
      // this.$refs['docForm'].dicts = this.dicts
    },
    btnViewClick (row, index) {
      this.$refs.dataTable.setCurrentRow(row)
      this.showEditor = true
      this.$refs['docForm'].title = '查 看'
      this.$refs['docForm'].keyNum = ''
      this.$refs['docForm'].isErrorDoc = false // 档号错误提示
      this.$refs['docForm'].isView = true
      this.$refs['docForm'].visible = true
      this.$refs['docForm'].loadCols(this.cols)
      this.$refs['docForm'].loadData(row)
      // this.$refs['docForm'].dicts = this.dicts
    },
    // 原件挂接
    btnMountClick (row, index) {
      console.log('row', row)
      this.$refs.dataTable.setCurrentRow(row)
      // this.$refs['mountForm'].isActive = true
      this.$refs['mountForm'].show(true)
      this.$refs['mountForm'].accept(row.DOCID)
      this.$refs['mountForm'].$el.firstChild.style.height = window.innerHeight - 295 + 'px'
      // var tmpObj = this.$refs['dataTable'].$refs['table'].$refs['bodyWrapper']
      // if (typeof (tmpObj) === 'undefined') {
      //   return
      // }
      // this.$refs['mountForm'].$el.firstChild.style.height = tmpObj.style.height window.innerHeight - 230
      // var rowHeight = 36 // event.target.parentNode.clientHeight
      // var distanceRight = 17
      // var tmpOffsetTop = row.$index - tmpObj.scrollTop / rowHeight
      // // this.$refs['mountForm'].$el.firstChild.style.maxHeight = tmpObj.style.height
      // this.$refs['mountForm'].$el.firstChild.style.right = distanceRight + 6 + 'px'
    },
    btnReplaceClick () {
      // this.$message('您点击了替换按钮')
      this.classId = this.classId
      this.classType = this.classType
      var manyparamsid = JSON.stringify(this.manyparamsid)
      var arr = JSON.parse(manyparamsid)
      if (arr.length === 0) {
        this.$message.error('请选择要替换的列表')
        return false
      } else {
        this.showReplace = true
        this.$refs['replaceForm'].title = '替换方式'
        this.$refs['replaceForm'].dialogFormVisible = true
        this.$refs['replaceForm'].init()
        this.$refs['replaceForm'].loadCols(this.option.column)
        this.$refs['replaceForm'].getNumber(this.option.column)
      }
    },
    btnDelClick (row, index) {
      let that = this
      this.classType = this.$route.params.classType
      if (typeof (this.classType) !== 'undefined' && this.classType !== '') {
        var deleteaction = ''
        var data = []
        var obj = {}
        switch (this.classType) {
          case '0':
            deleteaction = this.easyStarAPI.DeleteDocTable
            obj = {
              docid: row.DOCID
            }
            data.push(obj)
            break
          case '1':
            deleteaction = this.easyStarAPI.DeleteBoxTable
            obj = {
              boxid: row.BOXID
            }
            data.push(obj)
            break
          case '2':
            deleteaction = this.easyStarAPI.DeleteRoolTable
            obj = {
              roolid: row.ROOLID
            }
            data.push(obj)
            break
        }
        that.$confirm('此操作将删除该文件, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          that.axios.delete(that.api + deleteaction, {data:
            {
              data,
              flag: 1
            }}).then((res) => {
            console.log('删除的结果', res)
            if (res.data.code === 0) {
              this.fetchData()
              this.$message({
                type: 'success',
                message: '删除成功!'
              })
            } else {
              this.$message({
                type: 'error',
                message: res.data.data
              })
            }
          }).catch((error) => {
            console.log(error)
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          })
        })
      }
    },
    // 滚动栏钩子
    // scroll () {
    //   // const viewHeight = global.innerHeight
    //   let parentNode
    //   if (this.container !== global) {
    //     parentNode = this.$el
    //   } else {
    //     parentNode = this.$el.parentNode
    //   }
    //   // console.log(this.$el)
    //   if (parentNode) {
    //     // const rect = parentNode.getBoundingClientRect()
    //   }
    // },
    windowResize () {
      this.option.height = window.innerHeight - 230
      this.$refs['mountForm'].windowResize()
    },
    popShow: function () {
    },
    // 显示自定义报表
    btnPrintClick: function () {
      let that = this
      if (that.manyparamsid.length === 0) {
        this.$message.error('请选择要打印的列表')
        return false
      } else {
        this.visiblePrinte = true
        this.axios.get(this.api + this.easyStarAPI.ReportList).then((res) => {
          console.log('自定义报表结果', res)
          if (res.data.code === 0) {
            that.reports = res.data.list
          }
        }).catch(() => {
        })
      }
    },
    // 报表预览
    btnPreviewClick: function (report, index) {
      let that = this
      this.classType = this.$route.params.classType
      var repValue = []
      var i
      // var obj = {}
      if (typeof (this.classType) !== 'undefined' && this.classType !== '') {
        switch (this.classType) {
          case '0':
            for (i = 0; i < that.manyparamsid.length; i++) {
              // obj = {
              //   docid: that.manyparamsid[i]['DOCID']
              // }
              repValue.push(that.manyparamsid[i]['DOCID'])
              // repValue.push(obj)
            }
            break
          case '1':
            for (i = 0; i < that.manyparamsid.length; i++) {
              // obj = {
              //   boxid: that.manyparamsid[i]['BOXID']
              // }
              repValue.push(that.manyparamsid[i]['BOXID'])
              // repValue.push(obj)
            }
            break
          case '2':
            for (i = 0; i < that.manyparamsid.length; i++) {
              // obj = {
              //   roolid: that.manyparamsid[i]['ROOLID']
              // }
              repValue.push(that.manyparamsid[i]['ROOLID'])
              // repValue.push(obj)
            }
            break
        }
        // for (var i = 0; i < this.manyparamsid.length; i++) {
        //   var obj = {}
        //   for (var j = 0; j < this.cols.length; j++) {
        //     console.log('列', this.cols)
        //     var fieldname = this.cols[j]['FIELDNAME']
        //     var disstr = this.cols[j]['DISSTR']
        //     var str = this.manyparamsid[i][fieldname]
        //     obj[disstr] = str
        //   }
        //   repValue.push(obj)
        // }
        // this.axios.post(this.api + this.easyStarAPI.ReportPrint, {
        //   reportName: report.REPNAME,
        //   list: repValue
        // }, { responseType: 'arraybuffer' }).then((res) => {
        //   console.log('打印报表数据', res)
        //   const blob = new Blob([res.data], {type: 'application/pdf'})
        //   const fileName = report.REPNAME + '.pdf'
        //   if ('download' in document.createElement('a')) { // 非IE下载
        //     const elink = document.createElement('a')
        //     elink.download = fileName
        //     elink.style.display = 'none'
        //     elink.href = URL.createObjectURL(blob)
        //     document.body.appendChild(elink)
        //     elink.click()
        //     URL.revokeObjectURL(elink.href) // 释放URL 对象
        //     document.body.removeChild(elink)
        //   } else { // IE10+下载
        //     navigator.msSaveBlob(blob, fileName)
        //   }
        this.axios.post(this.api + this.easyStarAPI.ReportPrint, {
          reportName: report.REPNAME,
          list: repValue
        }).then((res) => {
          console.log('打印报表数据', res)// 把获取到的文件流做成一个url，并在新窗口打开
          let url = that.api + this.easyStarAPI.ReportPrint + '?id=' + res.data.id + '&name=' + encodeURI(report.REPNAME) + '.pdf'
          var tempwindow = window.open('_blank')// 先打开页面
          tempwindow.location = url// 后更改页面地址
        }).catch((error) => {
          console.log(error)
        })
      }
    },
    // 导入原件
    btnImportClick () {
      this.$refs['importForm'].visible = true
      this.$refs['importForm'].successResponse = false
      this.$refs['importForm'].loadCols(this.cols)
    },
    // 导出条目列表
    btnExportClick: function () {
      var manyparamsid = JSON.stringify(this.manyparamsid)
      var arr = JSON.parse(manyparamsid)
      if (arr.length === 0) {
        this.$message.error('请选择要导出的条目')
        return false
      } else {
        this.$refs['exportxls'].visiblePrinte = true
      }
    },
    exportForm: function (type, name) {
      let that = this
      this.classType = this.$route.params.classType
      var exportArr = []
      var i
      // var obj = {}
      if (typeof (this.classType) !== 'undefined' && this.classType !== '') {
        switch (this.classType) {
          case '0':
            for (i = 0; i < that.manyparamsid.length; i++) {
              // obj = {
              //   docid: that.manyparamsid[i]['DOCID']
              // }
              exportArr.push(that.manyparamsid[i]['DOCID'])
              // repValue.push(obj)
            }
            break
          case '1':
            for (i = 0; i < that.manyparamsid.length; i++) {
              // obj = {
              //   boxid: that.manyparamsid[i]['BOXID']
              // }
              exportArr.push(that.manyparamsid[i]['BOXID'])
              // repValue.push(obj)
            }
            break
          case '2':
            for (i = 0; i < that.manyparamsid.length; i++) {
              // obj = {
              //   roolid: that.manyparamsid[i]['ROOLID']
              // }
              exportArr.push(that.manyparamsid[i]['ROOLID'])
              // repValue.push(obj)
            }
            break
        }
        that.axios.post(that.api + that.easyStarAPI.Export, {
          type: type,
          list: exportArr
        }, {responseType: 'arraybuffer'}).then(function (res) {
          console.log('导出结果', res)
          const blob = new Blob([res.data], {type: 'application/xls;charset=utf-8'})
          const fileName = '导出' + name
          if ('download' in document.createElement('a')) { // 非IE下载
            const elink = document.createElement('a')
            elink.download = fileName
            elink.style.display = 'none'
            elink.href = URL.createObjectURL(blob)
            document.body.appendChild(elink)
            elink.click()
            URL.revokeObjectURL(elink.href) // 释放URL 对象
            document.body.removeChild(elink)
          } else { // IE10+下载
            navigator.msSaveBlob(blob, fileName)
          }
        }).catch(function (err) {
          console.log(err)
        })
      }
    },
    // 成批替换
    replaceSave (dataParams) {
      console.log('值', dataParams)
      let that = this
      this.classType = this.$route.params.classType
      var list = []
      var i
      // (分为文件级、盒级、案卷级)
      if (typeof (this.classType) !== 'undefined' && this.classType !== '') {
        var replaceaction = ''
        switch (this.classType) {
          case '0':
            replaceaction = this.easyStarAPI.DocReplace
            for (i = 0; i < that.manyparamsid.length; i++) {
              list.push(that.manyparamsid[i]['DOCID'])
            }
            dataParams.list = list
            break
          case '1':
            replaceaction = this.easyStarAPI.BoxReplace
            for (i = 0; i < that.manyparamsid.length; i++) {
              list.push(that.manyparamsid[i]['BOXID'])
            }
            dataParams.list = list
            break
          case '2':
            replaceaction = this.easyStarAPI.RoolReplace
            for (i = 0; i < that.manyparamsid.length; i++) {
              list.push(that.manyparamsid[i]['ROOLID'])
            }
            dataParams.list = list
            break
        }
        that.axios.post(that.api + replaceaction, dataParams).then((res) => {
          console.log('替换数据的结果', res)
          if (res.data.code === 0) {
            this.fetchData()
            this.$message({
              type: 'success',
              message: '批量替换成功!'
            })
          } else {
            this.$message({
              type: 'error',
              message: res.data.errMsg
            })
          }
        }).catch((error) => {
          console.log(error)
        })
      }
    },
    // 选中的数据
    selectionChange (list) {
      this.manyparamsid = list
    },
    // 批量删除
    btnDeleteManyClick: function () {
      let that = this
      this.classType = this.$route.params.classType
      var manyparamsid = JSON.stringify(this.manyparamsid)
      var arr = JSON.parse(manyparamsid)
      if (arr.length === 0) {
        this.$message.error('请选择要删除的列表')
        return false
      } else {
        if (typeof (this.classType) !== 'undefined' && this.classType !== '') {
          var deleteaction = ''
          var obj = {}
          var data = []
          var i = ''
          switch (this.classType) {
            case '0':
              deleteaction = this.easyStarAPI.DeleteDocTable
              for (i = 0; i < that.manyparamsid.length; i++) {
                obj = {
                  docid: that.manyparamsid[i]['DOCID']
                }
                data.push(obj)
              }
              break
            case '1':
              deleteaction = this.easyStarAPI.DeleteBoxTable
              for (i = 0; i < that.manyparamsid.length; i++) {
                obj = {
                  boxid: that.manyparamsid[i]['BOXID']
                }
                data.push(obj)
              }
              break
            case '2':
              deleteaction = this.easyStarAPI.DeleteRoolTable
              for (i = 0; i < that.manyparamsid.length; i++) {
                obj = {
                  roolid: that.manyparamsid[i]['ROOLID']
                }
                data.push(obj)
              }
              break
          }
          that.$confirm('此操作将删除选中的文件, 是否继续?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
          }).then(() => {
            that.axios.delete(that.api + deleteaction, {data:
              {
                data,
                flag: 0
              }}).then((res) => {
              console.log('删除的结果', res)
              if (res.data.code === 0) {
                this.fetchData()
                this.$message({
                  showClose: true,
                  type: 'success',
                  message: res.data.data
                })
              } else {
                this.$message({
                  showClose: true,
                  type: 'error',
                  message: res.data.data
                })
              }
            }).catch((error) => {
              console.log(error)
            })
          }).catch(() => {
            this.$message({
              type: 'info',
              message: '已取消批量删除'
            })
          })
        }
      }
    },
    searchForm: function (searchList) {
      console.log(searchList)
      this.keyWords = searchList
      // for (var i = 0; i < searchList.length; i++) {
      //   this.keyWords = searchList[i].value
      // }
      this.axios.post(this.api + this.easyStarAPI.Search, {
        data: searchList
      }).then((res) => {
        console.log('搜索的结果', res)
        if (res.data.code === 0) {
          if (res.data.list.length === 0) {
            this.$message({
              showClose: true,
              message: '暂无找到对应结果',
              type: 'error'
            })
          } else {
            this.data = res.data.list
            // this.changeColor(res.data.list)
          }
          this.page.total = res.data.count
        } else {
          this.$message({
            showClose: true,
            message: res.data.errMsg,
            type: 'error'
          })
        }
      }).catch((res) => {
        console.log(res)
      })
    },
    changeColor (resultsList) {
      let that = this
      resultsList.map((item, index) => {
        // debugger
        if (that.keyWords && that.keyWords.length > 0) {
          // 匹配关键字正则
          let replaceReg = new RegExp(that.keyWords[0].value, 'g')
          // 高亮替换v-html值
          let replaceString =
            '<span style="color:red">' + that.keyWords[0].value + '</span>'
          resultsList[index] = item.replace(
            replaceReg,
            replaceString
          )
        }
      })
      this.data = resultsList
    },
    // btnEmptyClick: function () {
    //   let that = this
    //   that.$confirm('此操作将清空所有文件, 是否继续?', '提示', {
    //     confirmButtonText: '确定',
    //     cancelButtonText: '取消',
    //     type: 'warning'
    //   }).then(() => {
    //     this.$message({
    //       type: 'success',
    //       message: '列表全部清空!'
    //     })
    //   }).catch(() => {
    //     this.$message({
    //       type: 'info',
    //       message: '已取消列表清空操作'
    //     })
    //   })
    // },
    closePrinteList: function (done) {
      this.visiblePrinte = false
    },
    // 显示原件列表
    showFiles: function (row, index) {
      this.$refs.dataTable.setCurrentRow(row)
      var tmpObj = this.$refs['dataTable'].$refs['table'].$refs['bodyWrapper'] // document.getElementsByClassName('el-table__body-wrapper')[0]
      if (typeof (tmpObj) === 'undefined') {
        return
      }
      if (this.currentDocId !== row.DOCID) {
        this.visible2 = true
        this.currentDocId = row.DOCID
      } else {
        this.visible2 = !this.visible2
      }
      var rowHeight = 36 // event.target.parentNode.clientHeight
      var tmpOffsetTop = row.$index - tmpObj.scrollTop / rowHeight
      // console.log(rowHeight, tmpObj.scrollTop)
      // console.log(tmpObj.scrollTop, tmpObj.offsetTop, row.$index, row.$index - tmpObj.scrollTop / 42)
      this.$refs['popover'].$el.firstChild.style.top = tmpOffsetTop * rowHeight + 20 + 'px'
      this.$refs['fileView'].getFiles(row.DOCID)
    },
    rowClick: function (row, event, column) {
      // console.log('行操作', row)
      if (this.$refs.mountForm.isActive) {
        this.$refs['mountForm'].accept(row.DOCID)
      }
      // console.log(this.$refs['dataTable'].$refs['table'].$refs['bodyWrapper'])
      // this.visible2 = false
    },
    currentChange: function (obj) {
      this.page.currentPage = obj
      this.fetchData()
    },
    refreshChange: function (obj) {
      this.fetchData()
    },
    rowUpdate: function (row, index, done, loading) {
      console.log('3=', row, index, done, loading)
    },
    sizeChange: function (pageSize) {
      this.page.pageSize = pageSize
      this.fetchData()
    },
    parseCols: function (cols) {
      var newCols = []
      cols.forEach(element => {
        var newCol = {}
        newCol.label = element.DISSTR.trim()
        newCol.prop = element.FIELDNAME.trim()
        newCol.ifdict = element.DICTTABLE.trim()
        newCol.filetype = element.FIELDTYPE.trim()
        newCol.overHidden = true
        if (element.FIELDNAME.trim() === 'FILENUM') {
          newCol.solt = true
          newCol.fixed = true
        }
        newCols.push(newCol)
      })
      // var arr = newCols.filter(r => (r.label !== '原件数量'))
      this.option.column = newCols
      // this.option.column = arr
    },
    fetchData: function () {
      let that = this
      // this.classType = this.$route.params.classType
      // if (typeof (this.classId) !== 'undefined' && typeof (this.classType) !== 'undefined' && this.classId !== '' && this.classType !== '') {
      if (typeof (this.classType) !== 'undefined' && this.classType !== '') {
        var url = ''
        switch (this.classType) {
          case '0':
            url = this.easyStarAPI.DocTable
            that.haveFileNum = true
            break
          case '1':
            url = this.easyStarAPI.BoxTable
            that.haveFileNum = false
            break
          case '2':
            url = this.easyStarAPI.RoolTable
            that.haveFileNum = false
            break
        }
        this.loading = true
        if (that.haveFileNum === false) {
          that.$refs['mountForm'].show(false)
        }
        // this.axios.get(this.api + this.easyStarAPI.GetDocFrame, {
        //   params: {
        //     classType: this.classType
        //   }
        // }).then(function (res) {
        //   console.log('GetDocFrame的结果', res)
        //   that.cols = res.data.cols
        //   // that.colArray = res.data.cols
        //   // console.log('that.colArray的结果', that.colArray)
        // })
        this.axios.get(this.api + url, {
          params: {
            pageSize: this.page.pageSize,
            pageIndex: this.page.currentPage
          }
        }).then(
          function (res) {
            console.log('结果', res)
            if (res.data.code === 0) {
              that.loading = false
              that.cols = res.data.cols
              that.parseCols(res.data.cols)
              that.data = res.data.list
              that.page.total = res.data.count
            } else {
              that.$message({
                showClose: true,
                message: res.data.errMsg,
                type: 'error'
              })
            }
          }
        ).catch(
          function () {
            that.loading = false
          }
        )
      }
    }
  }
}
</script>

<style>
.even {
  background: rgb(240, 240, 240);
}
.popperclass {
  position: absolute;
  left: 290px;
  border-radius: 10px;
  box-shadow:3px 3px 5px #000;
}
#DocMain .el-table--striped .el-table__body tr.el-table__row--striped.current-row td, .el-table__body tr.hover-row.current-row > td, .el-table__body tr.current-row > td {
  background: rgb(68, 208, 233)!important;
  font-weight: 700;
  color: #000;
}
#DocMain .avue-tip{
  display: none!important;
}
#DocMain .avue-crud__tip{
  display: none!important;
}
.el-card{
  background-color: #f4f9fa
}
#DocMain .el-table--mini td {
  padding: 0px;
}
#DocMain .el-table .cell {
  line-height: 22px!important;
}
</style>
<style scoped>
.reportItem {
  height: 30px;
  margin-left: 0px;
  padding-left: 5px;
  line-height: 30px;
  cursor: pointer;
  border-radius: 5px;
}
.reportItem:hover {
  background: rgb(68, 208, 233);
}
.el-scrollbar >>> .el-scrollbar__wrap{
  overflow-x: hidden;
}
/* .headerClass {
  height: 60px;
} */
.el-dialog__body{
  padding:0px 20px 30px!important;
}
</style>



