// 扫描组件
// 作者：LeonSu
// 属性：docId，类型：String
// 事件：uploadSuccess，上传成功，参数：无
<template>
  <div id="easystarscan" style="width: 99%; height: 99%; overflow: hidden">
    <el-row style="margin: 10px 0;">
        <el-button v-if="false" size="mini" type="success" @click="beginExport" style="margin-right: 20px;"><i class="el-icon-folder-opened"></i> 导出pdf</el-button>
        <el-button size="mini" @click="beginNew" style="background: yellow; border-color: yellow"><i class="el-icon-folder"></i> 新建</el-button>
        <el-button size="mini" type="success" @click="beginSave"><i class="el-icon-folder-checked"></i> 保存</el-button>
        <el-button size="mini" type="warning" @click="beginLoad"><i class="el-icon-folder-opened"></i> 读取</el-button>
        <el-button size="mini" type="primary" @click="beginScan" style="margin-right: 20px;margin-left: 20px;"><i class="el-icon-picture"></i> 扫描</el-button>
        <el-button size="mini" type="warning" @click="beginUpload"><i class="el-icon-upload"></i> 上传</el-button>
    </el-row>
    <el-row :gutter="24" type="flex" class="scanRow" style="width: 100%; 100%; margin: auto 0px;padding: 10px 0px;">
      <!-- <el-scrollbar style="height: 100%; width: 100%; overflow: scroll;"> -->
        <div style="width: 100%; height: 100%; overflow: hidden">
          <div id="imgTooles" ref="imgTools" style="width: 100%; overflow-y: scroll">
            <el-col v-for="(item, index) in this.imgList" :key="index" :xs="6" :sm="4" :md="3">
              <div style="background: white; width: 100px; height: 135px; display: flex; justify-content:center; align-items:Center; position: relative; border:1px solid black; margin-bottom: 10px;" @mouseenter="showDelPanel(item, true)" @mouseleave="showDelPanel(item, false)">
                <img :ref="'img' + index" :src="item.url" width="100px" class="imgStyle">
                <div ref="deletePanel" class="panelStyle" v-if="item.show">
                  <el-tooltip effect="dark" content="删除" placement="bottom">
                    <i class="el-icon-error" style="color: red; font-size: 22px; transform: translate(0%, 25%); cursor: pointer" @click="btnDelClick(index)"></i>
                  </el-tooltip>
                </div>
              </div>
            </el-col>
          </div>
        </div>
      <!-- </el-scrollbar> -->
    </el-row>
    <el-dialog
      :visible.sync="dialogVisible"
      width="30%"
      :close-on-click-modal="false"
      :close-on-press-escape="false"
      :show-close="false"
       top="30vh"
      class="mydialog">
      <i class="el-icon-loading" v-if="isWaiting" style="font-size: 100px; color: blue"></i>
      <el-progress v-if="!isWaiting" type="circle" color="#8e71c7" :stroke-width="6" :percentage="percent" style="color: yellow"></el-progress>
    </el-dialog>
  </div>
</template>

<script>
import Viewer from 'viewerjs'
import 'viewerjs/dist/viewer.css'
export default {
  name: 'EasyStarScan',
  components: {
    viewer: Viewer
  },
  props: {
    docId: String // 如果要把属性的变量的值保存到另一个变量中，就要使用watch，否则另一个变量的内容不会更新，最好是直接使用属性中的变量
  },
  data () {
    return {
      isWaiting: false,
      dialogVisible: false,
      percent: 0,
      showing: false,
      webSocket: null,
      lockReconnect: false,
      status: '',
      imgList: [],
      percentText: '',
      scanId: ''
    }
  },
  computed: {
    disHeight: function () {
      console.log(window.innerHeight)
      return window.innerHeight + 'px'
    },
    getStatus: function () {
      switch (status) {
        case 'state_scanning':
          return 'danger'
        default:
          return ''
      }
    }
  },
  mounted () {
    this.$nextTick(() => {
      this.initViewer()
    })
  },
  methods: {
    init () {
      this.$refs.imgTools.style.height = window.innerHeight - 158 + 'px'
      this.imgList = []
      this.webSocketInit()
    },
    close () {
      this.webSocket.onopen = null
      this.webSocket.onmessage = null
      this.webSocket.onclose = null
      this.webSocket.onerror = null
      this.webSocket.close()
      this.webSocket = null
    },
    windowResize () {
      this.$refs.imgTools.style.height = window.innerHeight - 158 + 'px'
    },
    getCookie () {
      // 获取cookie字符串
      var strCookie = document.cookie
      // 将多cookie切割为多个名/值对
      var arrCookie = strCookie.split('; ')
      // 遍历cookie数组，处理每个cookie对
      for (var i = 0; i < arrCookie.length; i++) {
        var arr = arrCookie[i].split('=')
        // 找到名称为userId的cookie，并返回它的值
        if (arr[0] === 'JSESSIONID') {
          return arr[1]
        }
      }
      return ''
    },
    initViewer () {
      const ViewerDom = document.getElementById('imgTooles')
      this.viewer = new Viewer(ViewerDom, {
        'inline': false,
        'button': true, // 右上角按钮
        'navbar': false, // 底部缩略图
        'title': true, // 当前图片标题
        'tooltip': true, // 显示缩放百分比
        'movable': true, // 是否可以移动
        'zoomable': true, // 是否可以缩放
        'rotatable': true, // 是否可旋转
        'scalable': true, // 是否可翻转
        'transition': false, // 使用 CSS3 过度
        'fullscreen': false, // 播放时是否全屏
        'keyboard': true, // 是否支持键盘
        toolbar: { // 底部工具栏
          zoomIn: {
            show: 1,
            size: 'large'
          },
          zoomOut: {
            show: 1,
            size: 'large'
          },
          oneToOne: {
            show: 1,
            size: 'large'
          },
          reset: {
            show: 0,
            size: 'large'
          },
          prev: {
            show: 1,
            size: 'large'
          },
          play: {
            show: 0,
            size: 'large'
          },
          next: {
            show: 1,
            size: 'large'
          },
          rotateLeft: {
            show: 1,
            size: 'large',
            click: this.rotateLeft
          },
          rotateRight: {
            show: 1,
            size: 'large',
            click: this.rotateRight
          },
          flipHorizontal: {
            show: 0,
            size: 'large'
          },
          flipVertical: {
            show: 0,
            size: 'large'
          }
        }
      })
      this.dialogVisible = false
    },
    webSocketInit () {
      this.connecting = true
      this.webSocket = new WebSocket('ws://' + this.WS_API + '/scan/tskj')
      this.webSocket.onopen = this.websocketonopen
      this.webSocket.onmessage = this.webSocketMessage
      this.webSocket.onclose = this.websocketclose
      this.webSocket.onerror = this.websocketerror
    },
    // webSocket接收服务器消息
    webSocketMessage (evt) {
      if (this.isDebug) {
        console.log(JSON.parse(evt.data))
      }
      var json = JSON.parse(evt.data)
      switch (json.code) {
        case -4:
        case -3:
        case -2:
        case -1:
          this.$notify({
            title: '错误信息',
            message: json.errMsg,
            type: 'error'
          })
          this.dialogVisible = false
          this.percent = 0
          this.percentText = ''
          break
        case 0:
          this.scanId = json.data
          break
        case 2:
          this.viewer.destroy()
          this.addImage(json.data)
          setTimeout(this.initViewer, 1000)
          this.$notify({
            title: '扫描结束',
            message: '扫描完成',
            type: 'success'
          })
          this.dialogVisible = false
          this.percent = 0
          this.percentText = ''
          break
        case 3:
          if (json.data <= 100) {
            this.percent = json.data
            if (json.data === 100) {
              this.percentText = 'success'
            }
          } else {
            if (json.data > 100) {
              this.percent = 99
            } else {
              this.percent = 0
            }
          }
          break
        case 4:
          this.status = json.data
          break
        case 5:
          // console.log(this.viewer)
          var item = this.imgList[this.viewer.index]
          item.url = item.srcUrl + '?t=' + Math.random()
          // if (item.width === 100) {
          //   item.width = 133
          //   item.height = 100
          // } else {
          //   item.with = 100
          //   item.height = 133
          // }
          this.$notify({
            title: '处理成功',
            message: '旋转图片成功',
            type: 'success'
          })
          this.dialogVisible = false
          this.percent = 0
          this.percentText = ''
          break
        case 6:
          // var item1 = this.imgList[this.viewer.index]
          this.imgList.splice(json.data, 1)
          this.$notify({
            title: '删除成功',
            message: '删除图片成功',
            type: 'success'
          })
          this.dialogVisible = false
          this.percent = 0
          this.percentText = ''
          break
        case 7:
          this.$emit('uploadSuccess')
          this.dialogVisible = false
          this.percent = 0
          this.percentText = ''
          this.$confirm('上传成功，\n需要新建扫描作业吗？', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'success'
          }).then(() => {
            this.confiremNew()
          })
          break
        case 9:
          this.dialogVisible = false
          this.viewer.destroy()
          this.$notify({
            title: '新建成功',
            message: '成功新建扫描作业',
            type: 'success'
          })
          this.imgList = []
          break
        case 10:
          this.dialogVisible = false
          this.$notify({
            title: '保存成功',
            message: '成功保存扫描作业',
            type: 'success'
          })
          break
        case 11:
          this.dialogVisible = false
          this.$notify({
            title: '加载成功',
            message: '成功读取扫描作业',
            type: 'success'
          })
          var jsonFiles = JSON.parse(json.data)
          for (var i = 0; i < jsonFiles.length; i++) {
            this.addImage(jsonFiles[i])
          }
          setTimeout(this.initViewer, 1000)
          break
        case 98:
          this.$notify({
            title: '错误信息',
            message: json.data,
            type: 'error'
          })
          this.dialogVisible = false
          this.percent = 0
          this.percentText = ''
          break
        default:
      }
    },
    // webSocket发送消息
    webSocketSend (data) {
      if (this.isDebug) {
        console.log(data)
      }
      this.webSocket.send(JSON.stringify(data))
    },
    // 断开重连
    reConnection () {
      if (this.lockReconnect) return
      this.lockReconnect = true
      // let that = this
      setTimeout(() => {
        this.webSocketInit()
        this.lockReconnect = false
      }, 5000)
    },
    sendBaseParam () {
      var data = {}
      data.command = 'setBaseParam'
      data.param = {}
      data.param.sessionId = this.getCookie()
      data.param.token = this.TOKEN
      data.param.host = 'http://192.168.1.108' // location.origin
      data.param.docId = this.docId
      this.webSocketSend(data)
    },
    websocketonopen () {
      if (this.isDebug) {
        console.log('WebSocket连接成功')
      }
      this.sendBaseParam()
      this.$notify({
        title: '连接成功',
        message: '已经连接接口服务',
        type: 'success'
      })
    },
    websocketclose (e) { // 关闭
      if (this.isDebug) {
        console.log('connection closed (' + e.code + ')')
      }
      this.$notify({
        title: '连接关闭',
        message: '接口服务已关闭',
        type: 'warn'
      })
      this.webSocket = null
      this.reConnection()
    },
    websocketerror (e) {
      if (this.isDebug) {
        console.log('WebSocket连接发生错误')
      }
      this.$notify({
        title: '连接失败',
        message: '不能连接接口服务',
        type: 'error'
      })
      this.reConnection()
    },
    addImage (imgUrl) {
      var img = {}
      img.url = 'http://' + this.WS_API + imgUrl
      img.srcUrl = img.url
      img.title = imgUrl
      img.show = false
      img.index = this.imgList.length
      this.imgList.push(img)
    },
    // 开始扫描
    beginScan () {
      this.dialogVisible = true
      this.isWaiting = false
      var data = {}
      data['command'] = 'scan'
      this.webSocketSend(data)
    },
    // 左旋转
    rotateLeft () {
      if (this.isDebug) {
        console.log('向左旋转')
      }
      this.beginRotate(-90)
    },
    // 右旋转
    rotateRight () {
      if (this.isDebug) {
        console.log('向右旋转')
      }
      this.beginRotate(90)
    },
    beginRotate (angle) {
      this.dialogVisible = true
      this.isWaiting = true
      this.viewer.rotate(angle)
      var data = {}
      data.command = 'rotate'
      data.param = {}
      data.param.angle = angle
      data.param.fileName = this.viewer.image.alt
      this.webSocketSend(data)
    },
    showDelPanel (item, visible) {
      item.show = visible
    },
    // 新建
    beginNew () {
      if (this.imgList.length >= 0) {
        this.$confirm('当前有扫描作业，确认需要继续吗？', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.confiremNew()
        })
      }
    },
    confiremNew () {
      this.dialogVisible = true
      this.isWaiting = true
      var data = {}
      data.command = 'new'
      data.param = {}
      data.param.files = []
      for (var i = 0; i < this.imgList.length; i++) {
        data.param.files.push(this.imgList[i].title)
      }
      this.webSocketSend(data)
    },
    // 保存
    beginSave () {
      if (this.imgList.length === 0) {
        this.$notify({
          title: '保存失败',
          message: '不能保存空白作业',
          type: 'error'
        })
        return false
      }
      this.$confirm('确定保存当前作业吗？', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.dialogVisible = true
        this.isWaiting = true
        var data = {}
        data.command = 'save'
        data.param = {}
        data.param.docId = this.docId
        data.param.files = []
        for (var i = 0; i < this.imgList.length; i++) {
          data.param.files.push(this.imgList[i].title)
        }
        this.webSocketSend(data)
      })
    },
    // 读取
    beginLoad () {
      if (this.imgList.length > 0) {
        this.$notify({
          title: '读取失败',
          message: '当前有图像，不能读取',
          type: 'error'
        })
        return false
      }
      this.dialogVisible = true
      this.isWaiting = true
      var data = {}
      data.command = 'load'
      data.param = {}
      data.param.docId = this.docId
      this.webSocketSend(data)
    },
    // 删除一页
    btnDelClick (index) {
      this.$confirm('确定上传PDF吗？', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.dialogVisible = true
        this.isWaiting = true
        var item = this.imgList[index]
        var data = {}
        data.command = 'delete'
        data.param = {}
        data.param.fileName = item.title
        data.param.index = index
        this.webSocketSend(data)
      })
    },
    beginExport () {
      this.dialogVisible = true
      this.isWaiting = false
      var data = {}
      data.scanId = this.scanId
      data.files = []
      for (var i = 0; i < this.imgList.length; i++) {
        data.files.push(this.imgList[i].title)
      }
      this.axios.post('http://127.0.0.1:51211/ExportToPdf.do', data, {
        responseType: 'blob'
      }).then((res) => {
        this.dialogVisible = false
        if (this.isDebug) {
          console.log(res)
        }
        const blob = new Blob([res.data])
        if ('download' in document.createElement('a')) { // 非IE下载
          const elink = document.createElement('a')
          elink.download = res.headers['filename']
          elink.style.display = 'none'
          elink.href = URL.createObjectURL(blob)
          document.body.appendChild(elink)
          elink.click()
          URL.revokeObjectURL(elink.href) // 释放URL 对象
          document.body.removeChild(elink)
        }
      }).catch((e) => {
        this.dialogVisible = false
        this.$notify({
          title: '导出失败',
          message: e,
          type: 'error'
        })
      })
    },
    // 转换成PDF，上传
    beginUpload () {
      this.$confirm('确定上传PDF吗？', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.dialogVisible = true
        this.isWaiting = true
        var data = {}
        data.command = 'upload'
        data.param = {}
        data.param.files = []
        for (var i = 0; i < this.imgList.length; i++) {
          data.param.files.push(this.imgList[i].title)
        }
        this.webSocketSend(data)
      })
    }
  }
}
</script>

<style>
#easystarscan {
  text-align: center;
}
.el-row {
  margin-bottom: 20px;
  &:last-child {
    margin-bottom: 0;
  }
}
.scanRow {
  background-image: url('/static/back.png');
  border: 1px solid gray;
}
.imgStyle {
  width: 100px;
  cursor: pointer;
  max-height: 133px;
  max-width: 100px;
  vertical-align: middle;
}
.panelStyle {
  width: 100px;
  position: absolute;
  bottom: 0px;
  background: transparent;
  color: #fff;
  background-color: rgba(0,0,0,0.25); /* <!--最后一个参数设置透明度，前面三个是RGB颜色值-->  */
  height: 30px;
  margin: 0 auto;
}
.mydialog > .el-dialog {
  background: transparent;
  box-shadow: 0 0 0 transparent;
}
.mydialog > .el-dialog > .el-dialog__header + .el-dialog__body > .el-progress--circle > .el-progress__text  {
  color: blue
}
</style>



