<template>
  <div>
    <el-dialog
      ref="popoverPrinter"
      title="导出条目列表"
      width="340px"
      :visible="visiblePrinte"
      :before-close="closePrinteList"
      >
      <div style="height: 150px">
        <el-scrollbar style="height: 100%" class="reportListView" :native="false" >
          <dl>
            <dd v-for="(item, index) in exportList" :key="item.id" class="reportItem" v-bind:class="{even: index % 2 === 0}" @click.stop="btnExportviewClick(item, index)">
              <span style="color: #000;">{{item.name}}</span>
            </dd>
          </dl>
        </el-scrollbar>
      </div>
      <div slot="footer" style="text-align: center;">
        <el-button size="mini" type="warning" @click="closePrinteList(true)">关闭</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
export default {
  name: 'Exportxls',
  data () {
    return {
      msg: '这是导出条目列表页面',
      visiblePrinte: false,
      exportList: [
        {
          id: 1,
          name: 'excel 2003',
          type: 0
        },
        {
          id: 2,
          name: 'excel 2007',
          type: 1
        },
        {
          id: 3,
          name: 'access 2003',
          type: 2
        },
        {
          id: 4,
          name: 'access 2007',
          type: 3
        }
      ]
    }
  },
  methods: {
    closePrinteList: function (done) {
      this.visiblePrinte = false
    },
    btnExportviewClick: function (item, index) {
      this.$emit('exportForm', item.type, item.name)
    }
  }
}
</script>
<style>
.reportListView{
  overflow-x:hidden;
}
.reportItem {
  height: 30px;
  margin-left: 0px;
  padding-left: 5px;
  line-height: 30px;
  cursor: pointer;
  border-radius: 5px;
}
.reportItem:hover {
  background: rgb(68, 208, 233);
}
</style>



