<template>
  <!-- 非标准档案导入组件 -->
  <div class="EasyStarImport">
    <el-dialog :visible="visible" @close="closeEditor" title="数据导入" style="padding: 10px;" width="400px" tooltip="数据导入">
      <el-form label-width="0px" size="mini">
        <el-row style="margin-bottom: 10px">
          <el-col>
            <el-upload
              class="upload-demo"
              ref="upload"
              :multiple="false"
              :show-file-list="false"
              accept=".xls,.xlsx,.mdb,.accdb"
              :action = "this.api + this.easyStarAPI.FileUpload"
              :auto-upload="true"
              :on-change="uploading"
              :on-success="onSuccess">
              <el-button slot="trigger" size="mini" type="primary">选取文件</el-button>
              <el-button style="margin-left: 10px;" size="mini" type="success" @click="startImport" :disabled="btnStart">开始导入</el-button>
              <div slot="tip" class="el-upload__tip">只能上传xls/xlsx/mdb/accdb文件，且不超过10MB</div>
            </el-upload>
          </el-col>
        </el-row>
        <el-row style="margin-bottom: 10px">
          <el-col>
            <el-form-item label-width="40px" label="主键" style="margin-bottom: 0px !important; margin-right: 10px;">
              <el-select clearable placeholder="请选择" v-model="keyFieldName" @change="checkKeyfield" class="chooseKey">
                <el-option value=""/>
                <el-option v-for="field in sourceFieldList" :key="field" :label="field" :value="field"/>
              </el-select>
              <span v-if="successResponse">
                <i class="el-icon-success success" v-if="successRes"></i>
                <i class="el-icon-error error" v-if="!successRes"></i>
              </span>
              <div>{{chooseKeyError}}</div>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col class="progressCol" v-if="ifShowProgress">
            <el-progress :percentage="progressPencent">{{finished}}</el-progress>
            <el-form-item v-if="!progressRes">
              <div v-for="item in progressResult" :key="item.keyField">
                <span>{{item.keyField}}</span>
                <span>{{item.msg}}</span>
              </div>
            </el-form-item>
          </el-col>
          <el-col>
            <el-table v-loading="loading2" size="mini" :data="targetFieldList" stripe border height="350"
              :header-cell-style="tableHeaderColor"
              :row-style="changeRowColor"
              @selection-change="handleSelectionChange">
              <el-table-column type="selection" width="35"/>
              <el-table-column label="目标字段" prop="DISSTR"/>
              <el-table-column label="来源字段">
                <template slot-scope="scope">
                  <el-form-item label="" style="margin-bottom: 0px !important;" size="mini">
                    <el-select clearable placeholder="请选择" v-model="scope.row.target" @change="checkField(scope.$index,scope.row)" class="chooseTarget">
                      <el-option value=""/>
                      <el-option v-for="field in sourceFieldList" :key="field" :label="field" :value="field"/>
                    </el-select>
                  </el-form-item>
                </template>
              </el-table-column>
              <el-table-column label="" prop="showRes" width="50">
                <template slot-scope="scope">
                  <span v-if="scope.row.target">
                    <i :class="scope.row.showRes === 'aaa' ? 'el-icon-success success':'el-icon-error error'"></i>
                  </span>
                </template>
              </el-table-column>
            </el-table>
          </el-col>
        </el-row>
        <el-row style="margin-top:5px;">
          <el-col>
            <el-form-item style="margin-bottom:0px;">
              <el-radio-group v-model="groupRadio" @change="getRadioValue">
                <el-radio :label="0">跳过</el-radio>
                <el-radio :label="1">覆盖</el-radio>
              </el-radio-group>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: 'EasyStarImport',
  data () {
    return {
      keyFieldName: '',
      visible: false,
      btnStart: false, // 开始导入按钮禁用
      successResponse: false, // 选择主键并成功拿到返回的结果
      successRes: true,
      chooseKeyError: '', // 选择主键返回的错误结果
      checkData: Boolean,
      ifShowProgress: false, // 进度条
      finished: '',
      progressPencent: 0,
      progressRes: true,
      timer: null,
      progressResult: [], // 进度条返回的结果
      groupRadio: '',
      targetFieldList: [],
      docCols: [], // 档号组成项
      normalCols: [], // 非档号组成项
      sourceFieldList: [],
      multipleSelection: [],
      loading2: false // 加载项
    }
  },
  components: {
  },
  methods: {
    // 修改table header的背景色
    tableHeaderColor ({ row, column, rowIndex, columnIndex }) {
      if (rowIndex === 0) {
        return 'color: #000; text-align: center;'
      }
    },
    changeRowColor ({ row, rowIndex }) {
      if (row.FIELDSTATE === 'F' || row.FIELDSTATE === 'U') {
        if (row.DOCNO !== '1') {
          return 'color: red;'
        }
      }
    },
    // 关闭对话框
    closeEditor () {
      this.visible = false
      this.keyFieldName = ''
      this.targetFieldList = []
      this.sourceFieldList = []
      this.multipleSelection = []
      clearInterval(this.timer)
      this.timer = null
    },
    // 加载列
    loadCols (cols) {
      this.targetFieldList = JSON.parse(JSON.stringify(cols))
      clearInterval(this.timer)
      this.timer = null
    },
    uploading (file, fileList) {
      if (file.status !== 'success') {
        this.loading2 = true
      }
    },
    // 文件上传成功
    onSuccess (response, file, fileList) {
      if (response.result === 0) {
        this.loading2 = false
        this.sourceFieldList = response.files[0].fields
        this.setAssField()
      }
    },
    // 进度条
    setTimer () {
      this.ifShowProgress = true
      if (this.timer == null) {
        this.timer = setInterval(() => {
          this.axios.get(this.api + this.easyStarAPI.UploadMessageServlet).then((res) => {
            // console.log('进度条结果', res)
            this.progressPencent = parseInt(res.data.progress)
            if (res.data.result === 0) {
              if (res.failure !== 0) {
                clearInterval(this.timer)
                this.progressRes = false
                this.progressResult = res.data.values
              } else {
                this.$parent.fetchData()
              }
              if (res.data.finished === true) {
                this.finished = '上传完成'
                clearInterval(this.timer)
                // this.timer = null
              } else {
                this.finished = '上传中'
              }
            } else if (res.data.result === 1) {
              clearInterval(this.timer)
              this.ifShowProgress = false
              this.$message.error(res.data.errMsg)
            }
            // if (res.failure !== 0) {
            //   this.progressRes = false
            //   this.progressResult = res.data.values
            // }
            // if (res.data.finished === true) {
            //   this.finished = '上传完成'
            //   setInterval(() => {
            //     this.ifShowProgress = false
            //   }, 3000)
            //   clearInterval(this.timer)
            //   this.timer = null
            // } else {
            //   this.finished = '上传中'
            // }
          })
        }, 1000)
      }
    },
    // 开始导入
    startImport () {
      // this.multipleSelection.find((item) => {
      //   if (item.DOCNO === '0') {
      //     console.log('22222')
      //   }
      // })
      if (this.keyFieldName === '' || this.successRes === false) {
        this.$message.error('请选择主键')
      } else if (this.groupRadio === '' || this.groupRadio === 'undefined') {
        this.$message.error('请选择跳过还是覆盖')
      } else {
        this.importList()
        // this.setTimer()
      }
    },
    // 导入条目
    importList () {
      var jsonSend = {}
      jsonSend.keyfield = this.keyFieldName
      jsonSend.mode = this.groupRadio
      jsonSend.assfields = {}
      for (let index in this.multipleSelection) {
        let item = this.targetFieldList[index]
        if (item.target) {
          jsonSend.assfields[item.FIELDNAME] = item.target
        } else {
          jsonSend.assfields[item.FIELDNAME] = ''
        }
      }
      this.axios.post(this.api + this.easyStarAPI.StartImportFiles,
        jsonSend
      ).then((res) => {
        console.log('开始导入结果', res)
        if (res.data.result === 0) {
          this.ifShowProgress = true
          this.setTimer()
          // this.$parent.fetchData()
        } else {
          this.$message.error(res.data.errMsg)
        }
      })
    },
    // 获取单选按钮的值
    getRadioValue (val) {
      this.groupRadio = val
    },
    // 选择主键
    checkKeyfield (val) {
      console.log('val', val)
      this.successResponse = false
      let obj = {}
      obj = this.sourceFieldList.find((item) => {
        return item === val
      })
      if (obj !== '' && obj !== undefined) {
        this.keyFieldName = obj // 获取prop的值
        console.log('keyFieldName的值', this.keyFieldName)
      }
      this.axios.post(this.api + this.easyStarAPI.VerifyFieldAction, {
        type: 1,
        fieldname: this.keyFieldName
      }).then((res) => {
        this.successResponse = true
        if (res.data.result === 0) {
          this.successRes = true
          this.chooseKeyError = ''
        } else {
          this.successRes = false
          this.chooseKeyError = res.data.errmsg
        }
      })
    },
    // 勾选要导入的列
    handleSelectionChange (val) {
      this.multipleSelection = val
      console.log('勾选的列', this.multipleSelection)
    },
    checkField (index, row) {
      // console.log(row)
      this.datavalid(row)
    },
    // 自动匹配对应关系
    setAssField () {
      for (let s1 in this.targetFieldList) {
        for (let s3 in this.sourceFieldList) {
          if (this.targetFieldList[s1].DISSTR === this.sourceFieldList[s3]) {
            // this.targetFieldList[s1].target = this.sourceFieldList[s3]
            this.$set(this.targetFieldList[s1], 'target', this.sourceFieldList[s3])
            this.$set(this.targetFieldList[s1], 'showRes', 'aaa')
            // console.log('ss', this.targetFieldList[s1])
            this.datavalid(this.targetFieldList[s1])
            break
          }
        }
      }
    },
    async datavalid (n1) {
      await this.axios.post(this.api + this.easyStarAPI.VerifyFieldAction, {
        type: 2,
        sourcefieldname: n1.target,
        targetfieldname: n1.FIELDNAME
      }).then((res) => {
        // console.log('改变数据返回的结果', res)
        if (res.data.result === 0) {
          this.$set(n1, 'showRes', 'aaa')
        } else {
          this.$set(n1, 'showRes', 'bbb')
          console.log('n1', n1)
        }
      })
    }
  },
  created: function () {
    // 每次进入界面时，先清除之前的所有定时器，然后启动新的定时器
    clearInterval(this.timer)
    this.timer = null
  },
  destroyed: function () {
    // 每次离开当前界面时，清除定时器
    clearInterval(this.timer)
    this.timer = null
  }
}
</script>

<style scoped>
.filesView >>> .el-scrollbar__wrap{
  overflow-x: hidden;
}
.headerClass {
  color: #000;
}
/* .EasyStarImport .el-form-item__content {
  width: 100%!important;
} */
.EasyStarImport .chooseKey, .EasyStarImport .chooseTarget {
  width: 83%!important;
}
.EasyStarImport .success {
  font-size: 18px;
  color: #2173d2;
}
.EasyStarImport .error {
  font-size: 18px;
  color: red;
}
.EasyStarImport .progressCol {
  position: absolute;
  z-index: 2;
  top: 28%;
  background: rgba(255,255,255, 1);
  /* color: #fff; */
  padding: 20px;
  height: 27%;
  overflow: auto;
  border: 1px solid #ccc;
  width: 94%;
}
</style>



