<template>
  <div>
    <transition name="trans">
      <div class="collapse-wrap" @click.stop id="panel" ref="panel" v-show="isActive" style="position: absolute; right: 0; width: 25%; top: 0; bottom: 0; background: #eee; z-index: 4;">
        <el-row style="top:80px; padding:0 20px;">
          <el-col :span="8" style="padding:20px 0px;">
            <el-button size="mini" style="color:#fff; background-color:#f00; border-color:#f00" @click.stop="hide" title="关闭">关闭</el-button>
          </el-col>
          <el-col :span="16" style="padding:20px 0px; text-align:center">
            <el-button size="mini" style="color:#fff; background-color: #409eff; border-color: #409eff; float:left;" @click.stop="btnSearchClick" title="搜索">搜索</el-button>
            <el-button size="mini" style="color:#fff; background-color:#e6a23c; border-color:#e6a23c; float:left;" @click.stop="btnRemoveClick" title="重置">重置</el-button>
            <el-button v-show="isShow" size="mini" style="float:left;" @click.stop="btnAlldataClick" title="返回">返回</el-button>
          </el-col>
          <el-col :span="12" style="padding:0px 0px 20px 0; font-size:0.875em;">
            字段：
            <el-select size="mini" v-model="searchregion" value-key="id" placeholder="请选择" class="select_search" @change="onSelectField" title="请选择字段">
              <el-option v-for="item in searCols" :key="item.FIELDNAME" :value="item.FIELDNAME" :label="item.DISSTR"></el-option>
            </el-select>
          </el-col>
          <el-col :span="12" style="padding:0px 0px 20px 0; font-size:0.875em;">
            约束：
            <el-select size="mini" v-model="searchcondition" placeholder="请选择" class="select_relation" @change="onOperation" title="请选择约束">
              <el-option v-for="item in searchConstrArray" :key="item.index" :value="item.ys_prop" :label="item.label"></el-option>
            </el-select>
          </el-col>
          <el-col :span="24" style="padding:0px 0px 20px 0; font-size:0.875em;">
            内容：<el-input size="mini" v-model="searchcontent" class="select_searchcontent" title="请输入搜索内容"></el-input>
          </el-col>
          <el-col :span="20" style="padding:0px 0px 20px 0; font-size:0.875em;">
            关系：
            <el-select size="mini" v-model="searchrelation" placeholder="请选择" class="select_bind" @change="onRelation" title="请选择关系">
              <el-option v-for="item in searchRelationArray" :key="item.index" :value="item.gx_prop" :label="item.label"></el-option>
            </el-select>
          </el-col>
          <!-- <el-col :span="22" style="padding:0px 0px 20px 0;">
            请输入搜索内容：<el-input v-model="searchcontent" class="select_searchcontent"></el-input>
          </el-col> -->
          <el-col :span="4" style="padding:0px 0px 20px 0;">
            <i class="el-icon-circle-plus" title="添 加" style="font-size:34px; color:#409EFF; cursor: pointer;" @click.stop="btnSearchAddClick"></i>
          </el-col>
          <el-col :span="24">
            <el-table class="tb-edit" border highlight-current-row :data="tableData" style="width: 100%; height:auto; max-height:650px; overflow-y:scroll; font-size:0.875em;">
              <template v-for="(col ,index) in searchcols">
                <el-table-column align="center" :prop="col.prop" :label="col.label" :key="index"></el-table-column>
              </template>
              <el-table-column
                  fixed="right"
                  label="操作"
                  width="100"
                  align="center">
                  <template slot-scope="scope">
                    <i class="el-icon-delete" title="删 除" style="color:#f00; cursor: pointer;" @click.stop="btnSearchDeleteClick(scope.row, scope.$index)"></i>
                  </template>
                </el-table-column>
              <!-- <template slot-scope="scope" slot="menu" style="text-align: left">
                <i class="el-icon-delete" title="删 除"></i>
              </template> -->
            </el-table>
          </el-col>
        </el-row>
      </div>
      </transition>
    </div>
</template>
<script>
export default {
  name: 'Search',
  props: [
    'searCols'
  ],
  data () {
    return {
      // searchregion: {},
      // searchcondition: {},
      // searchrelation: {},
      searchregion: '',
      searchcondition: '',
      searchrelation: '',
      searchcontent: '',
      obj1: {},
      obj2: {},
      obj3: {},
      isActive: false,
      isShow: false,
      searchConstrArray: [{
        id: '0',
        label: '等于',
        ys_prop: '0'
      }, {
        id: '1',
        label: '不等于',
        ys_prop: '1'
      }, {
        id: '2',
        label: '大于',
        ys_prop: '2'
      }, {
        id: '3',
        label: '小于等于',
        ys_prop: '3'
      }, {
        id: '4',
        label: '小于',
        ys_prop: '4'
      }, {
        id: '5',
        label: '大于等于',
        ys_prop: '5'
      }, {
        id: '6',
        label: '包含',
        ys_prop: '6'
      }, {
        id: '7',
        label: '不包含',
        ys_prop: '7'
      }],
      searchRelationArray: [{
        label: '并且',
        gx_prop: '0'
      }, {
        label: '或者',
        gx_prop: '1'
      }],
      searchcols: [
        {label: '字段', prop: 'searchFiled'},
        {label: '约束', prop: 'searchCondition'},
        {label: '关系', prop: 'searchRelation'},
        {label: '内容', prop: 'searchContent'}
      ],
      tableData: [
        // {
        //   node: '0061',
        //   name: '机库门',
        //   type: 'GPS',
        //   coordinate: '45.41,67.45,78.6'
        // }
      ]
    }
  },
  methods: {
    onSelectField (val) {
      let obj = {}
      obj = this.searCols.find((item) => {
        return item.FIELDNAME === val
      })
      if (obj !== '' && obj !== undefined) {
        this.searchregion = obj.FIELDNAME // 获取prop的值
        this.obj1 = obj
      }
      // let obj = {}
      // obj = this.searCols.find((item) => {
      //   return item
      // })
      // if (obj !== '' && obj !== undefined) {
      //   this.searchregion.FIELDNAME = val
      //   this.searchregion = obj
      // }
      // console.log('this.searchregion得值', this.searchregion)
    },
    onOperation (val) {
      let obj = {}
      obj = this.searchConstrArray.find((item) => {
        return item.ys_prop === val
      })
      if (obj !== '' && obj !== undefined) {
        this.searchcondition = obj.ys_prop// 获取prop的值
        this.obj2 = obj
      }
    },
    onRelation (val) {
      let obj = {}
      obj = this.searchRelationArray.find((item) => {
        return item.gx_prop === val
      })
      if (obj !== '' && obj !== undefined) {
        this.searchrelation = obj.gx_prop // 获取prop的值
        this.obj3 = obj
      }
    },
    hide: function () {
      this.isActive = false
      this.isShow = false
      document.body.removeEventListener('click', this.hide, false)
      this.tableData = []
      this.searchregion = ''
      this.searchcondition = ''
      this.searchrelation = ''
      this.searchcontent = ''
    },
    show (showing) {
      this.isActive = showing
      if (this.isActive) {
        document.body.addEventListener('click', this.hide, false)
      } else {
        document.body.removeEventListener('click', this.hide, false)
      }
    },
    btnSearchClick () {
      var list = []
      if (this.tableData.length === 0) {
        this.$notify({
          title: '错误',
          message: '请先将条件添加到下面的表格中',
          duration: 0,
          type: 'error'
        })
        return false
      } else {
        for (var i = 0; i < this.tableData.length; i++) {
          var obj = {
            fieldName: this.tableData[i].filedEnglish,
            operation: this.tableData[i].conditionNumber,
            relation: this.tableData[i].relationNumber,
            value: this.tableData[i].searchContent
          }
          list.push(obj)
        }
        this.$emit('searchForm', list)
        this.isShow = true
      }
    },
    btnRemoveClick: function () { // 点击重置
      this.tableData = []
      this.isShow = false
      this.searchregion = ''
      this.searchcondition = ''
      this.searchrelation = ''
      this.searchcontent = ''
    },
    btnAlldataClick: function () { // 返回
      this.$emit('alldata')
    },
    btnSearchAddClick: function () { // 点击添加
      if (this.obj1 === '' || this.obj2 === '' || this.obj3 === '' || this.searchcontent === '') {
        this.$notify({
          title: '错误',
          message: '数据填写不完整，请重新填写',
          duration: 0,
          type: 'error'
        })
        return false
      } else {
        for (var i = 0; i < this.tableData.length; i++) {
          if (this.searchregion === this.tableData[i].filedEnglish && this.searchcondition === this.tableData[i].conditionNumber && this.searchrelation === this.tableData[i].relationNumber) {
            this.$notify({
              message: '查找的条件有重复，请重新输入新条件'
            })
            return false
          }
        }
        this.tableData.push({
          searchFiled: this.obj1.DISSTR,
          filedEnglish: this.searchregion,
          searchCondition: this.obj2.label,
          conditionNumber: this.searchcondition,
          searchRelation: this.obj3.label,
          relationNumber: this.searchrelation,
          searchContent: this.searchcontent
        })
        console.log('this.tableData得值', this.tableData)
      }
    },
    btnSearchDeleteClick: function (row, index) { // 点击删除按钮
      this.tableData.splice(index, 1)
    }
  }
}
</script>
<style>
.collapse-wrap {
  transition: 0.25s linear;
}
.trans-enter-active {
  opacity: 0;
  transform: translate(100%, 0);
}
.trans-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
  transform: translate(100%, 0);
}
.el-select.select_search{
  width:68%!important;
}
.el-select.select_relation{
  width:69%!important;
}
.el-select.select_bind{
  width:38%!important;
}
.select_searchcontent{
  width:78%!important;
}
</style>



