<template>
  <div class="ReplaceForm">
    <el-dialog :title="title" :visible="dialogFormVisible" :append-to-body='true' :before-close="handleClose" :close-on-click-modal="false">
      <el-form :model="form" class="demo-form-inline" :inline="true">
        <!-- <el-form-item label="替换方式"> -->
          <el-radio-group v-model="radioStyle" style="display:block; margin-bottom:20px;" v-if="haveInt">
            <el-radio v-for="item in radioArray" :key="item.label" :label="item.label" @change="getNewData">{{item.name}}</el-radio>
              <!-- <el-radio :label="1" style="margin-top:10px;" @change="getNewData">前缀</el-radio>
              <el-radio :label="2" style="margin-top:10px;" @change="getNewData">后缀</el-radio>
              <el-radio :label="3" style="margin-top:10px;" @change="getNewData">部分替换</el-radio>
              <el-radio :label="4" style="margin-top:10px;" @change="getNewData">全部替换</el-radio>
              <el-radio :label="5" style="margin-top:10px;" @change="getNewData">+/-N</el-radio>
              <el-radio :label="6" style="margin-top:10px;" @change="getNewData">字段替换</el-radio> -->
          </el-radio-group>
          <el-radio-group v-model="radioStyle" style="display:block; margin-bottom:20px;" v-if="!haveInt">
            <el-radio v-for="item in nointradio" :key="item.label" :label="item.label" @change="getNewData">{{item.name}}</el-radio>
              <!-- <el-radio :label="1" style="margin-top:10px;" @change="getNewData">前缀</el-radio>
              <el-radio :label="2" style="margin-top:10px;" @change="getNewData">后缀</el-radio>
              <el-radio :label="3" style="margin-top:10px;" @change="getNewData">部分替换</el-radio>
              <el-radio :label="4" style="margin-top:10px;" @change="getNewData">全部替换</el-radio>
              <el-radio :label="5" style="margin-top:10px;" @change="getNewData">+/-N</el-radio>
              <el-radio :label="6" style="margin-top:10px;" @change="getNewData">字段替换</el-radio> -->
          </el-radio-group>
          <!-- <el-radio-group v-model="radioStyle" style="display:block;" v-if="Diction">
            <el-radio v-for="item in radioArray1" :key="item.label" :label="item.label" @change="getNewData">{{item.name}}</el-radio>
          </el-radio-group> -->
        <!-- </el-form-item> -->
        <!-- <el-form-item label="选择字段" v-if="chooseKey">
            <el-select v-model="form.region" placeholder="请选择字段" @change="changeLable">
              <el-option v-for="item in colArray" :key="item.index" :value="item.prop" :label="item.label"></el-option>
            </el-select>
        </el-form-item> -->
        <div class="inlineblock" v-if="radioStyle == '1'">
          <el-form-item label="选择字段">
            <el-select v-model="form.leftprefix" placeholder="请选择字段" @change="changeFirstLable">
              <el-option v-for="item in noDicts" :key="item.index" :value="item.prop" :label="item.label"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="前缀内容" style="margin-right:0;">
            <el-input size="medium" v-model="form.prefix" clearable></el-input>
          </el-form-item>
        </div>
        <div class="inlineblock" v-if="radioStyle == '2'">
          <el-form-item label="选择字段">
            <el-select v-model="form.leftsuffix" placeholder="请选择字段" @change="changeTwoLable">
              <el-option v-for="item in noDicts" :key="item.index" :value="item.prop" :label="item.label"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="后缀内容" style="margin-right:0;">
            <el-input size="medium" v-model="form.suffix" clearable></el-input>
          </el-form-item>
        </div>
        <div class="inlineblock" v-if="radioStyle == '3'">
          <el-form-item label="选择字段">
            <el-select v-model="form.leftoldData" placeholder="请选择字段" @change="changeThreeLable">
              <el-option v-for="item in noDicts" :key="item.index" :value="item.prop" :label="item.label"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="旧内容" style="margin-right:0;">
            <el-input size="medium" v-model="form.oldData" clearable></el-input>
          </el-form-item>
          <el-form-item label="替换成新内容" style="margin-right:0;">
            <el-input size="medium" v-model="form.newData" clearable></el-input>
          </el-form-item>
        </div>
        <div class="inlineblock" v-if="radioStyle == '4'">
          <el-form-item label="选择字段">
            <el-select v-model="form.leftbothContent" placeholder="请选择字段" @change="changeLable">
              <el-option v-for="item in colArray" :key="item.index" :value="item.prop" :label="item.label"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="替换成新内容" style="margin-right:0;" v-if="!Diction">
            <el-input size="medium" v-model="form.newContent" clearable></el-input>
          </el-form-item>
          <el-form-item label="替换成新内容" style="margin-right:0;" v-if="Diction">
            <!-- <el-input size="medium" v-model="form.newContent" clearable></el-input> -->
            <el-select v-model="form.newContent" placeholder="请选择" @change="changeDicts">
              <el-option v-for="item in DictsArray" :key="item.index" :value="item.DVALUE" :label="item.DVALUE"></el-option>
            </el-select>
          </el-form-item>
        </div>
        <div class="inlineblock" v-if="radioStyle == '5'">
          <el-form-item label="选择字段">
            <el-select v-model="form.leftnumber" placeholder="请选择字段" @change="changeFiveLable">
              <el-option v-for="item in numArray" :key="item.index" :value="item.prop" :label="item.label"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="+/-N(针对数值型)" style="margin-right:0;">
            <el-input size="medium" v-model="form.addNewContent" clearable></el-input>
          </el-form-item>
        </div>
        <div class="inlineblock" v-if="radioStyle == '6'">
          <el-form-item label="选择字段">
            <el-select v-model="form.leftfield" placeholder="请选择字段" @change="changeSixLable">
              <el-option v-for="item in noDicts" :key="item.index" :value="item.prop" :label="item.label"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="要替换的字段">
            <el-select v-model="form.fieldcontent" placeholder="请选择替换字段">
              <el-option v-for="item in noDicts" :key="item.index" :value="item.prop" :label="item.label"></el-option>
            </el-select>
          </el-form-item>
        </div>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="closeDialog">取 消</el-button>
        <el-button type="primary" @click="saveReplace">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
export default {
  name: 'ReplaceForm',
  props: // ['title', 'data2', 'dicts'],
  {
    adddatadesc: {
      type: String
    },
    colArray: {
      type: Array // 含有字典项的数组
    },
    manyparamsid: {
      type: Array // 获取list数组
    },
    deletedictArray: [] // 去掉字典项的数组
  },
  data () {
    return {
      haveInt: true,
      chooseKey: false,
      radioStyle: '',
      type: '',
      dataParams: {},
      form: {
        leftprefix: '', // 左边前缀
        prefix: '', // 前缀内容
        leftsuffix: '', // 左边后缀
        suffix: '', // 后缀内容
        leftoldData: '', // 左边部分替换
        oldData: '', // 中间部分替换
        newData: '', // 右边部分替换
        leftbothContent: '', // 左边全部替换
        newContent: '', // 右边全部替换
        leftnumber: '', // 左边+/-N
        addNewContent: '', // 右边+/-N
        leftfield: '', // 左边字段替换
        fieldcontent: '' // 右边字段替换
      },
      radioArray: [
        {
          name: '前缀',
          label: 1
        },
        {
          name: '后缀',
          label: 2
        },
        {
          name: '部分替换',
          label: 3
        },
        {
          name: '全部替换',
          label: 4
        },
        {
          name: '+/-N',
          label: 5
        },
        {
          name: '字段替换',
          label: 6
        }
      ],
      nointradio: [
        {
          name: '前缀',
          label: 1
        },
        {
          name: '后缀',
          label: 2
        },
        {
          name: '部分替换',
          label: 3
        },
        {
          name: '全部替换',
          label: 4
        },
        {
          name: '字段替换',
          label: 6
        }
      ],
      radioArray1: [
        {
          name: '全部替换',
          label: 4
        }
      ],
      title: '编 辑',
      showBorder: false,
      showStripe: false,
      showHeader: true,
      showIndex: false,
      showCheckbox: false,
      showPage: false,
      sizeValue: '',
      dialogTableVisible: false,
      dialogFormVisible: false,
      noDicts: [], // 无字典项数组
      dicts: {}, // 获取字典项
      Diction: false, // 判断是否是字典项
      DictsArray: [], // 自定义字典项数组
      numArray: [] // 数值型数组
    }
  },
  // computed: {
  //   option0 () {
  //     return {
  //       title: '',
  //       menuWidth: 380,
  //       border: this.showBorder,
  //       stripe: this.showStripe,
  //       showHeader: this.showHeader,
  //       index: this.showIndex,
  //       size: this.sizeValue,
  //       selection: this.showCheckbox,
  //       page: this.showPage,
  //       align: 'center',
  //       menuAlign: 'center'
  //     }
  //   }
  // },
  mounted () {
    let that = this
    this.axios.get(this.api + this.easyStarAPI.GetDictList).then(function (res) {
      that.dicts = res.data.list
      // console.log('获取对应的字典项', that.dicts)
    })
  },
  methods: {
    // getCols: function (classId, classType) {
    //   let that = this
    //   this.axios.get(this.api + this.easyStarAPI.GetDocFrame, {
    //     params: {
    //       classId: classId,
    //       classType: classType
    //     }
    //   }).then(function (res) {
    //     console.log('GetCols的结果', res)
    //     that.list = res.data.cols
    //     console.log('data的结果', that.data)
    //   })
    // },
    init: function () {
      this.radioStyle = 0
    },
    changeLable: function (val) {
      this.form.leftbothContent = ''
      this.form.newContent = ''
      let obj = {}
      obj = this.colArray.find((item) => {
        return item.prop === val
      })
      if (obj !== '' && obj !== undefined) {
        this.form.leftbothContent = obj.prop // 获取prop的值
        if (this.dicts.hasOwnProperty(this.form.leftbothContent)) {
          this.Diction = true
          this.DictsArray = this.dicts[this.form.leftbothContent]
        } else {
          this.Diction = false
        }
      }
    },
    // 前缀
    changeFirstLable: function (val) {
      this.form.prefix = ''
      this.judgeDicts(val, this.noDicts, this.form.leftprefix)
    },
    // 后缀
    changeTwoLable: function (val) {
      this.form.suffix = ''
      this.judgeDicts(val, this.noDicts, this.form.leftsuffix)
    },
    // 部分替换
    changeThreeLable: function (val) {
      this.form.oldData = ''
      this.form.newData = ''
      this.judgeDicts(val, this.noDicts, this.form.leftoldData)
    },
    // +/-N
    changeFiveLable: function (val) {
      this.form.addNewContent = ''
      this.judgeDicts(val, this.noDicts, this.form.leftnumber)
    },
    // 字段替换
    changeSixLable: function (val) {
      this.form.fieldcontent = ''
      this.judgeDicts(val, this.noDicts, this.form.leftfield)
    },
    changeDicts (val) {
      let obj1 = {}
      obj1 = this.DictsArray.find((item) => {
        return item.DVALUE === val
      })
      if (obj1 !== '' && obj1 !== undefined) {
        this.form.newContent = obj1.DVALUE // 获取DVALUE的值
      }
    },
    // 判断字典项
    judgeDicts (val, arr, variable) {
      variable = ''
      let obj = {}
      obj = arr.find((item) => {
        return item.prop === val
      })
      if (obj !== '' && obj !== undefined) {
        variable = obj.prop // 获取prop的值
      }
    },
    loadCols (cols) {
      this.noDicts = []
      for (var i = 0; i < cols.length; i++) {
        var element = cols[i]
        if (element.ifdict.trim() === '') {
          this.noDicts.push(element)
        }
      }
    },
    getNumber (cols) {
      this.numArray = []
      for (var i = 0; i < cols.length; i++) {
        var element = cols[i]
        if (element.ifdict.trim() === '') {
          if (element.filetype === 'I') {
            this.numArray.push(element)
          }
        }
      }
      this.showInt(this.numArray)
      console.log('numArray', this.numArray)
    },
    showInt (arr) {
      if (arr.length === 0) {
        this.haveInt = false
      }
    },
    handleEdit () {
      this.dialogFormVisible = true // 显示弹窗
      this.form = {
        leftprefix: '', // 左边前缀
        prefix: '', // 前缀内容
        leftsuffix: '', // 左边后缀
        suffix: '', // 后缀内容
        leftoldData: '', // 左边部分替换
        oldData: '', // 中间部分替换
        newData: '', // 右边部分替换
        leftbothContent: '', // 左边全部替换
        newContent: '', // 右边全部替换
        leftnumber: '', // 左边+/-N
        addNewContent: '', // 右边+/-N
        leftfield: '', // 左边字段替换
        fieldcontent: '' // 右边字段替换
      }
    },
    closeDialog: function () {
      this.dialogFormVisible = false
      this.form = {
        leftprefix: '', // 左边前缀
        prefix: '', // 前缀内容
        leftsuffix: '', // 左边后缀
        suffix: '', // 后缀内容
        leftoldData: '', // 左边部分替换
        oldData: '', // 中间部分替换
        newData: '', // 右边部分替换
        leftbothContent: '', // 左边全部替换
        newContent: '', // 右边全部替换
        leftnumber: '', // 左边+/-N
        addNewContent: '', // 右边+/-N
        leftfield: '', // 左边字段替换
        fieldcontent: '' // 右边字段替换
      }
      this.radioStyle = 0
    },
    saveReplace: function () {
      let that = this
      // var manyparamsid = []
      // for (var i = 0; i < this.manyparamsid.length; i++) {
      //   manyparamsid.push(this.manyparamsid[i]['DOCID'])
      // }
      switch (this.type) {
        case 0:
          that.dataParams = {
            type: this.type,
            sourceFieldName: this.form.leftprefix,
            newValue: this.form.prefix
          }
          this.$emit('replaceSave', that.dataParams)
          break
        case 1:
          that.dataParams = {
            type: this.type,
            sourceFieldName: this.form.leftsuffix,
            newValue: this.form.suffix
          }
          this.$emit('replaceSave', that.dataParams)
          break
        case 2:
          that.dataParams = {
            type: this.type,
            sourceFieldName: this.form.leftoldData,
            oldValue: this.form.oldData,
            newValue: this.form.newData
          }
          this.$emit('replaceSave', that.dataParams)
          break
        case 3:
          that.dataParams = {
            type: this.type,
            sourceFieldName: this.form.leftbothContent,
            newValue: this.form.newContent
          }
          this.$emit('replaceSave', that.dataParams)
          break
        case 4:
          that.dataParams = {
            type: this.type,
            sourceFieldName: this.form.leftnumber,
            addValue: this.form.addNewContent
          }
          this.$emit('replaceSave', that.dataParams)
          break
        case 5:
          that.dataParams = {
            type: this.type,
            sourceFieldName: this.form.leftfield,
            targetFieldName: this.form.fieldcontent
            // list: arr
          }
          this.$emit('replaceSave', that.dataParams)
          break
      }
      this.dialogFormVisible = false
      this.form = {
        leftprefix: '', // 左边前缀
        prefix: '', // 前缀内容
        leftsuffix: '', // 左边后缀
        suffix: '', // 后缀内容
        leftoldData: '', // 左边部分替换
        oldData: '', // 中间部分替换
        newData: '', // 右边部分替换
        leftbothContent: '', // 左边全部替换
        newContent: '', // 右边全部替换
        leftnumber: '', // 左边+/-N
        addNewContent: '', // 右边+/-N
        leftfield: '', // 左边字段替换
        fieldcontent: '' // 右边字段替换
      }
    },
    getNewData () {
      let that = this
      that.type = 0
      switch (this.radioStyle) {
        case 1:
          that.type = 0
          break
        case 2:
          that.type = 1
          break
        case 3:
          that.type = 2
          break
        case 4:
          that.type = 3
          break
        case 5:
          that.type = 4
          break
        case 6:
          that.type = 5
          break
      }
      that.form = {
        leftprefix: '', // 左边前缀
        prefix: '', // 前缀内容
        leftsuffix: '', // 左边后缀
        suffix: '', // 后缀内容
        leftoldData: '', // 左边部分替换
        oldData: '', // 中间部分替换
        newData: '', // 右边部分替换
        leftbothContent: '', // 左边全部替换
        newContent: '', // 右边全部替换
        leftnumber: '', // 左边+/-N
        addNewContent: '', // 右边+/-N
        leftfield: '', // 左边字段替换
        fieldcontent: '' // 右边字段替换
      }
      console.log('that.type', that.type)
    },
    handleClose: function () {
      this.dialogFormVisible = false
    }
  }
}
</script>
<style>
.avue-crud__menu {
  min-height:50px;
}
.ReplaceForm .el-form-item__content{
  width:91%;
}
/* .el-select-dropdown{
  top: 28%!important;
} */
.inlineblock{
  display: inline-block!important;
}
</style>



