<template>
  <div style="height: 88px;text-align: center" v-bind:class="{hasFiles: files.length > 0,noFiles:files.length == 0}" v-loading="loading">
    <el-row v-if="files.length > 0" :gutter="24" ref="row" style="height: 88px;">
      <el-scrollbar style="height:100%;" class="filesView" :native="false">
        <el-col :span="6" v-for="file in files" :key="file.FILEID" style="margin:0px;height:auto">
          <el-tooltip :content="calcFileSize(file.FILESIZE)" placement="bottom">
          <div style="border: 0px;text-align:center; height: 100%" :title="file.DESCRIOTIO">
            <img :src="'../../static/assets/files/' + file.FILETYPE.toLowerCase() + '_48px.png'" style="width:32px; height: 32px;box-shadow:5px 2px 6px #000"/>
            <div style="padding: 0px; font-size: 12px">
              {{ file.DESCRIOTIO }}
            </div>
          </div>
          </el-tooltip>
        </el-col>
      </el-scrollbar>
    </el-row>
    <span v-else style="font-size:larger;font-weight:700;color:#f00">无原件</span>
  </div>
</template>
<script>
export default {
  name: 'FilesView',
  // created: function () {
  //   this.$nextTick(function () {
  //     this.getFiles()
  //   })
  // },
  data () {
    return {
      docId: '',
      loading: false,
      count: 0,
      emptyFile: {
        DESCRIOTIO: '无数据'
      },
      files: []
    }
  },
  methods: {
    calcFileSize: function (fileSize) {
      var MAX_KB = Math.pow(1024, 2)
      if (fileSize >= 0 && fileSize <= MAX_KB) {
        return (fileSize / 1024).toFixed(2) + ' KB'
      }
      var MAX_MB = Math.pow(1024, 3)
      if (fileSize >= MAX_KB && fileSize <= MAX_MB) {
        return (fileSize / MAX_KB).toFixed(2) + ' MB'
      }
      var MAX_GB = Math.pow(1024, 4)
      if (fileSize >= MAX_MB && fileSize <= MAX_GB) {
        return (fileSize / MAX_MB).toFixed(2) + ' GB'
      }
      return (fileSize / MAX_GB).toFixed(2) + ' TB'
    },
    getFiles: function (d) {
      let that = this
      that.loading = true
      that.docId = d
      this.axios.get(this.api + this.easyStarAPI.GetFiles, {
        params: {
          docId: d
        }
      }).then(function (res) {
        that.loading = false
        if (res.data.code === 0) {
          that.files = res.data.list
        } else {
          that.files = []
        }
      }).catch(function () {
        that.loading = false
        that.files = []
      })
    }
  }
}
</script>
<style>
</style>
<style scoped>
.filesView >>> .el-scrollbar__wrap{
  overflow-x: hidden;
}
.hasFiles {
  width: 600px;
}
.noFiles {
  width: 100%;
  line-height: 88px;
}
</style>



