<template>
  <!-- 档案条目信息查看编辑功能组件 -->
  <div class="DocFormTable" v-bind:class="{showEditor: visible, hideEditor: !visible}">
    <el-dialog :visible="visible" @close="closeEditor" :title="title" :close-on-click-modal="false" style="padding: 10px;" tooltip="asdf">
      <el-dialog width="30%" :title="resultdesc" :visible.sync="innerVisible" :show-close="false" append-to-body>
        <div>{{adddatadesc}}</div>
        <div slot="footer" class="dialog-footer">
          <el-button size="mini" type="primary" @click="closeDialog">确 定</el-button>
        </div>
      </el-dialog>
      <div v-if="visible" style="max-height: 430px; height: 430px; overflow: hidden; width: 100%">
        <el-form size="medium" :rules="formRules" :inline="true" label-width="80px" style="max-height: 430px; height: 430px; padding: 0px; text-align: left;">
          <!-- 档号 -->
          <div v-if="docNoCol && typeof (docNoCol.FIELDNAME) != 'undefined'">
              <el-form-item
                size="mini"
                :label="docNoCol.DISSTR.trim()"
                style="display: inline;"
                >
                <span v-if="isNew" style="color: #00f; font-weight: bold;">{{docNo}}</span>
                <span v-else style="color: #00f; font-weight: bold;">{{data[docNoCol.FIELDNAME.trim()]}}</span>
                <span style="margin-left: 10px; color: red;" v-if="isErrorDoc">{{errdocNo}}</span>
              </el-form-item>
          </div>
          <el-scrollbar style="height: 355px;" class="filesView" ref="elscrollbar" :noresize="true">
            <!-- 档号组成项 -->
            <el-row>
              <el-col style="height:60px;" :xs="24" :sm="24" :md="24" :lg="12" v-for="col in docCols" :key="col.FIELDNAME">
                <el-form-item
                  :prop="col.FIELDNAME"
                  size="mini"
                  :label="col.DISSTR.trim()"
                  style="display: inline;"
                  class="isDocNoArray"
                  v-bind:class="{addstar: col.ISFILL=='0'}">
                  <el-input
                    class="DocForm"
                    clearable
                    v-model="data[col.FIELDNAME]"
                    v-if="col.DICTTABLE == ''"
                    :maxlength="(col.FIELDTYPE == 'I' && col.FIELDSIZE == 0) ? 10 : col.FIELDSIZE"
                    :placeholder="col.DISSTR"
                    :readonly="isView"
                    v-bind:class="{isViewStyle: isView}"
                    @clear="onDocNoInputClear()"
                    @blur="onDocNoInputChange($event, col.FIELDNAME, col.FIELDSIZE)"/>
                  <el-select
                    class="DocForm"
                    v-else
                    type="select"
                    clearable
                    v-model="data[col.FIELDNAME]"
                    value=""
                    :disabled="isView"
                    :placeholder="col.DISSTR"
                    @change="onDocNoSelectChange(data[col.FIELDNAME], col.FIELDNAME)">
                    <el-option :readonly="isView" size="mini" v-for="dict in dicts[col.FIELDNAME]" :key="dict.DICTID" :value="dict.DVALUE" :label="dict.DVALUE"/>
                  </el-select>
                </el-form-item>
              </el-col>
            </el-row>
            <!-- 普通项 -->
            <el-row>
              <el-col :xs="24" :sm="24" :md="24" :lg="12" v-for="(col, index) in normalCols" :key="col.FIELDNAME">
                <el-form-item
                  size="mini"
                  :label="col.DISSTR.trim()"
                  style="display: inline;"
                  v-bind:class="{
                    isDocNo: col.FIELDNAME=='DOCNO',
                    isDocNoArray: col.DOCNO!='1' && col.FIELDNAME!='DOCNO',
                    addstar: col.ISFILL=='0'}">
                  <el-input
                    class="DocForm"
                    clearable
                    v-model="data[col.FIELDNAME]"
                    v-if="col.DICTTABLE == ''"
                    :maxlength="(col.FIELDTYPE === 'I' && col.FIELDSIZE === 0) ? 10 : col.FIELDSIZE"
                    :readonly="isView"
                    v-bind:class="{isViewStyle: isView}"
                    :placeholder="col.DISSTR.trim()"/>
                  <el-select :disabled="isView" class="DocForm" v-else type="select" clearable v-model="data[col.FIELDNAME]" value="" :placeholder="col.DISSTR">
                    <!-- <el-option size="mini" v-for="dict in dicts[col.FIELDNAME]" :key="dict.DICTID" :value="dict.DICTID" :label="dict.DVALUE"/> -->
                    <el-option size="mini" v-for="dict in dicts[col.FIELDNAME]" :key="dict.DICTID" :value="dict.DVALUE" :label="dict.DVALUE"/>
                  </el-select>
                </el-form-item>
                <span style="display: inline-block; margin-left: 70px; font-size: 0.75em; color: red;" v-if="(keyNum === index) ? true : false">{{normalError}}</span>
              </el-col>
            </el-row>
          </el-scrollbar>
        </el-form>
      </div>
      <div slot="footer" style="text-align: center;">
        <div v-if="!isView">
          <el-button size="mini" type="warning" @click="btnResetClick">重 置</el-button>
          <el-button size="mini" type="primary" @click="btnSaveClick">保 存</el-button>
        </div>
        <div v-else>
          <el-button size="mini" type="primary" @click="closeEditor">关 闭</el-button>
        </div>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import * as tools from '@/util/tools'
export default {
  name: 'DocForm',
  props:
  {
    adddatadesc: {
      type: String // 描述情况
    },
    classId: {
      type: String
    },
    classType: {
      type: String
    }
  },
  data () {
    var validatePass = (rule, value, callback) => {
      console.log(rule, value)
      if (value === '') {
        callback(new Error('请输入'))
      } else {
        callback()
      }
    }
    return {
      formRules: {
        col: [
          {validator: validatePass, trigger: 'blur'}
        ]
      },
      docNo: '',
      errdocNo: '', // 生成档号错误提示
      isErrorDoc: false,
      title: '编 辑',
      isView: false,
      isNew: false,
      visible: false,
      innerVisible: false,
      oldData: {},
      data: {},
      cols: [],
      docCols: [], // getDocCols(this.cols),
      docNoCol: {},
      normalCols: [], // getNormalCols(this.cols)
      resultdesc: '', // 弹窗提示
      // isQuit: false,
      // isFirst: true,
      dicts: {}, // 获取字典项
      arr: [],
      paramsId: '', // 创建档号的条目id
      partNum: '', // 件号的值
      fSize: '', // 字段长度
      keyNum: '', // 索引
      normalError: '' // 错误提示（普通项）
    }
  },
  // beforeUpdate: function () {
  //   this.$nextTick(function () {
  //     let that = this
  //     if (that.isFirst && !that.isQuit) {
  //       this.axios.get(this.api + this.easyStarAPI.GetDictList).then(function (res) {
  //         console.log('GetDictList的结果', res)
  //         that.dicts = res.data.list
  //         that.isFirst = false
  //         that.isQuit = true
  //       })
  //     }
  //   })
  // },
  mounted: function () {
    let that = this
    this.axios.get(this.api + this.easyStarAPI.GetDictList).then(function (res) {
      console.log('GetDictList的结果', res)
      that.dicts = res.data.list
    })
  },
  methods: {
    showParams (val) {
      this.paramsId = val
    },
    getDValue (fieldName, dValue) {
      var tmpDictId = ''
      this.dicts[fieldName].forEach(dict => {
        if (dict.DVALUE.trim() === dValue.trim()) {
          tmpDictId = dict.DICTID.trim()
          return false
        }
      })
      return tmpDictId
    },
    // 创建新条目，分配新的档案ID
    createNewDoc () {
      this.isNew = true
      var newDocId = tools.uuid()
      this.data['DOCID'] = newDocId
    },
    // 向后台请求创建新的档号
    createNewDocNo () {
      let that = this
      var dictValue = {}
      var objData = {}
      switch (this.title) {
        case '新 增':
          objData = {
            classId: this.classId,
            classType: this.classType,
            values: dictValue,
            docId: '',
            newOrOld: '0'
          }
          break
        case '修 改':
          objData = {
            classId: this.classId,
            classType: this.classType,
            values: dictValue,
            docId: this.paramsId,
            newOrOld: '1'
          }
          break
      }
      that.docCols.forEach(element => {
        var tmpS = that.data[element.FIELDNAME.trim()]
        if (typeof (that.data[element.FIELDNAME]) !== 'undefined') {
          if (element.DICTTABLE !== '') {
            if (tmpS === null) {
              tmpS = ''
            } else {
              tmpS = tmpS.trim()
            }
            if (tmpS === '') {
              dictValue[element.FIELDNAME.trim() + '_ID'] = ''
            } else {
              dictValue[element.FIELDNAME.trim() + '_ID'] = this.getDValue(element.FIELDNAME.trim(), tmpS)
            }
          }
          if (tmpS === '') {
            dictValue[element.FIELDNAME.trim()] = tmpS
          } else {
            dictValue[element.FIELDNAME.trim()] = that.data[element.FIELDNAME.trim()].trim()
          }
          // dictValue[element.FIELDNAME.trim()] = that.data[element.FIELDNAME.trim()].trim()
        }
      })
      this.axios.post(this.api + this.easyStarAPI.NewDocNo, objData).then((res) => {
        if (res.data.code === 0) {
          that.isErrorDoc = false
          if (that.isNew) {
            that.docNo = res.data.docNo
          }
          that.data[that.docNoCol.FIELDNAME.trim()] = res.data.docNo
        } else {
          // this.$message({
          //   type: 'error',
          //   message: res.data.errMsg
          // })
          that.isErrorDoc = true
          if (that.isNew) {
            that.docNo = res.data.docNo
          }
          that.data[that.docNoCol.FIELDNAME.trim()] = res.data.docNo
          that.errdocNo = res.data.errMsg
        }
      }).catch((err) => {
        console.log('向后台请求创建新的档号的报错', err)
        tools.parseResponse(this, '接口', err)
      })
    },
    // 档号组成项（普通输入框）的内容清空时
    onDocNoInputClear () {
      this.createNewDocNo()
    },
    // 档号组成项（普通输入框）的焦点移开时
    onDocNoInputChange (event, fieldName, fieldSize) {
      // console.log(event.target.value, fieldName)
      this.fSize = fieldSize
      if (typeof (this.data[fieldName]) === 'undefined') {
        return false
      }
      var tmpValue = this.data[fieldName].trim()
      if (fieldName === 'NOTENO') {
        tmpValue = tmpValue.replace(/[^0-9-]+/, '')
      }
      var tmpLen = tmpValue.length
      tmpValue = Array(fieldSize - tmpLen + 1).join(0).concat(tmpValue)
      // 如果该档号组成项的值没有发现变化，则不重新创建档号
      // console.log(this.data[fieldName].trim(), tmpValue)
      if (this.oldData[fieldName] === tmpValue) {
        return false
      }
      this.data[fieldName] = tmpValue
      this.createNewDocNo()
    },
    // 档号组成项（下拉选择框）的焦点移开时
    onDocNoSelectChange (value, fieldName) {
      if (typeof (this.data[fieldName]) === 'undefined') {
        return false
      }
      this.createNewDocNo()
    },
    loadData (data) {
      var obj = {}
      obj = JSON.parse(JSON.stringify(data))
      this.oldData = obj
      var obj1 = {}
      obj1 = JSON.parse(JSON.stringify(data))
      this.data = obj1
    },
    loadCols (cols) {
      let that = this
      that.cols = cols
      that.docCols = []
      that.normalCols = []
      that.docNoCol = {}
      for (var i = 0; i < cols.length; i++) {
        var element = cols[i]
        if (element.FIELDSTATE === 'F' || element.FIELDSTATE === 'U') {
          if (element.FIELDNAME.trim() === 'DOCNO') {
            that.docNoCol = element
          } else {
            if (element.DOCNO !== '1') {
              that.docCols.push(element)
            } else {
              that.normalCols.push(element)
            }
          }
        }
      }
    },
    closeEditor () {
      this.docNo = ''
      this.visible = false
      this.cols = []
      this.data = {}
      this.oldData = {}
      this.docCols = []
      this.normalCols = []
      this.docNoCol = {}
    },
    closeDialog () {
      switch (this.title) {
        case '新 增':
          if (this.adddatadesc !== '新增数据成功') {
            this.innerVisible = false
            // this.docNo = ''
          } else {
            this.innerVisible = false
            if (this.partNum !== '' && typeof (this.partNum) !== 'string') {
              this.partNum++
              this.partNum = this.partNum.toString() // 数字转字符串
              var tmpLen = this.partNum.length
              this.partNum = Array(this.fSize - tmpLen + 1).join(0).concat(this.partNum)
            }
            for (var i = 0; i < this.cols.length; i++) {
              var element = this.cols[i]
              if (element.DOCNO !== '1') {
                // this.data[element.FIELDNAME] = (element.DISSTR === '件号') ? this.partNum : (this.data[element.FIELDNAME])
                if (element.DISSTR === '件号') {
                  this.$set(this.data, element.FIELDNAME, this.partNum)
                } else {
                  this.$set(this.data, element.FIELDNAME, (this.data[element.FIELDNAME]))
                }
              } else {
                // this.data[element.FIELDNAME] = ''
                this.$set(this.data, element.FIELDNAME, '')
              }
            }
            if (this.partNum + 1) {
              this.createNewDocNo()
            }
          }
          break
        case '修 改':
          this.innerVisible = false
          this.visible = false
          break
        // case '2':
          //   addaction = this.easyStarAPI.RoolTable
          //   break
      }
      // this.docCols = []
    },
    btnResetClick () {
      this.data = JSON.parse(JSON.stringify(this.oldData))
    },
    btnSaveClick () {
      var vData = {}
      this.arr = []
      vData = JSON.parse(JSON.stringify(this.data))
      this.docCols.find((item) => {
        // var dataLen = this.data[item.FIELDNAME]
        if (item.DISSTR === '件号') {
          if (typeof (this.data[item.FIELDNAME]) !== 'undefined') {
            if (this.data[item.FIELDNAME].trim() !== '') {
              this.partNum = this.data[item.FIELDNAME]
              this.partNum = parseInt(this.partNum)
            }
          }
        }
        // if (this.data[item.FIELDNAME].trim() !== '' && item.DICTTABLE === '') {
        //   var conLength = (item.FIELDTYPE === 'I' && item.FIELDSIZE === 0) ? 10 : item.FIELDSIZE
        //   console.log('22222222', conLength)
        //   console.log('1111111', dataLen.length)
        //   if (dataLen.length > conLength) {
        //     console.log('长度有误')
        //   }
        // }
      })
      for (var i = 0; i < this.cols.length; i++) {
        var item = this.cols[i]
        var obj = {}
        if (item.ISFILL === '0') {
          var str = 'REQUIRE'
          var val = item.FIELDNAME
          this.$set(obj, 'k', i - (this.docCols.length) - 1) // 设置k值获取index
          obj[str] = val
          this.arr.push(obj)
        } else {
          if (typeof (this.data[item.FIELDNAME]) === 'undefined' || this.data[item.FIELDNAME] === '') {
            if (item.FIELDTYPE === 'I') {
              vData[item.FIELDNAME] = '0'
            } else {
              vData[item.FIELDNAME] = ''
            }
          }
        }
      }
      // console.log('arr的值', this.arr)
      for (var y = 0; y < this.arr.length; y++) {
        if (this.isNew === true) {
          var requireValue = vData[this.arr[y].REQUIRE]
          this.keyNum = this.arr[y].k
        } else {
          requireValue = vData[this.arr[y].REQUIRE]
          this.keyNum = this.arr[y].k
        }
        if (requireValue === '' || typeof (requireValue) === 'undefined') {
          this.normalError = '必填项未填写完全'
          return false
        } else {
          this.normalError = ''
        }
      }
      if (this.isErrorDoc === false) {
        this.$emit('dataSave', this.isNew, this.cols, vData)
      }
      // this.innerVisible = true
      switch (this.title) {
        case '新 增':
          this.resultdesc = '新 增'
          break
        case '修 改':
          this.resultdesc = '修 改'
          break
          // case '2':
            //   addaction = this.easyStarAPI.RoolTable
            //   break
      }
    }
  }
}
</script>
<style>
/* .DocForm {
  width: 200% !important;
} */
.DocFormTable .el-form-item__content {
  width: 60% !important;
}
.addstar .el-form-item__label:after {
  color: red;
  content: '* ';
  font-size: 110%;
  display: inline;
}
.DocFormTable .isDocNo .el-form-item__label {
  color: blue !important;
}
.DocFormTable .isDocNoArray .el-form-item__label {
  color: red !important;
  width: 80px !important;
  font-size: 0.875em!important;
}
.DocFormTable .isViewStyle input {
  background: rgb(245, 247, 250);
}
.DocFormTable .el-dialog{
  width: 58%;
}
.DocFormTable .el-form-item__label{
  text-align: left;
}
.DocFormTable .el-input--mini input{
  height: 36px !important;
}
</style>
<style scoped>
.filesView >>> .el-scrollbar__wrap{
  overflow-x: hidden;
}
.DocForm >>> .el-input__inner {
  padding-right: 5px;
  padding-left: 5px;
  color: black;
}
.showEditor {
  position: absolute;
}
.hideEditor {
  position: inherit;
  display: none;
}
</style>



