<template>
  <div>
    <transition name="trans">
      <div class="collapse-wrap mount_content" @click.stop id="panel" ref="panel" v-show="isActive">
        <el-row>
          <el-col :span="24">
            <i class="iconfont icon-fanhui btnBack" title="返 回" @click.stop="hide"></i>
            <i class="el-icon-refresh btnRefresh" title="刷 新" @click.stop="viewFile"></i>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="24">
            <div class="mount_text">原件列表</div>
          </el-col>
        </el-row>
        <el-row class="searchRow">
          <el-col :span="24">
            <el-input class="search"
              v-model="serachContent"
              placeholder="请输入查询内容"
              title="请输入查询内容"
              clearable>
              <el-button slot="append" icon="el-icon-search" title="搜索" @click.stop="searchOnlyFile()"></el-button>
            </el-input>
          </el-col>
        </el-row>
        <el-row class="searchRow">
          <el-col :span="12" style="height: 42px; line-height: 42px;">
            <div style="color: #999;margin-left: 5px;font-size: 1em;">共{{fileData.length}}个原件</div>
          </el-col>
          <el-col :span="12">
            <!-- <div style="color: #999;float:right; margin-right: 5px;font-size: 1em; visibility: hidden;">删除全部 <i class="el-icon-delete" title="删 除" style="cursor: pointer; color:red;" ></i></div> -->
            <el-button class="btnScanning" @click.stop="btnScanClick">在线扫描</el-button>
          </el-col>
        </el-row>
        <el-row style="margin:0px;width: 90%; margin: 0 auto; height: 65%; overflow-y: auto;" v-loading="fullscreenLoading">
          <el-col :span="8" class="moufile" v-for="item in fileData" :key="item.FILEID">
            <div class="moufile_content" style="margin: 10px 0px;" @click.stop="viewFilePDF(item)">
              <svg class="icon" aria-hidden="true" style="font-size:1.5em; cursor: pointer; padding: 0.6em;">
                <title>查看{{item.DESCRIOTIO}}</title>
                <use xlink:href="#icon-pdf"></use>
              </svg>
              <i class="el-icon-delete btnDelete" title="删 除" @click.stop="deleteManyFile(item)"></i>
              <!-- <el-progress type="circle" class="circle" :percentage="50"></el-progress> -->
              <div :title="item.DESCRIOTIO" class="mount_message" style="">{{item.DESCRIOTIO}}</div>
            </div>
          </el-col>
          <el-col :span="8" class="mouadd">
            <div class="mouadd_content" style="margin: 10px 0px; text-align:center;">
              <el-upload
              class="upload-demo docmain_upload"
              ref="upload"
              :multiple="true"
              :show-file-list="false"
              accept="application/pdf"
              :action="api + easyStarAPI.FileUploadAction"
              :before-upload="beforeAvatarUpload"
              :http-request="uploadImg">
              <i class="el-icon-upload" style="font-size: 1.6em; cursor: pointer; color: #409EFF; padding: 0.6em;" title="上传原件"></i>
              <div class="el-upload__text" style="color: #666; font-size: 0.8em;" title="上传原件">上传原件</div>
            </el-upload>
            </div>
          </el-col>
        </el-row>
        <el-row style="margin-top: 10px; text-align: center;">
          <el-col :span="24">
            <div style="color: #999;font-size: 1em;">格式支持：PDF格式</div>
          </el-col>
        </el-row>
      </div>
    </transition>
    <el-dialog :fullscreen="true" @opened="openScan" @close="closeScan" :visible.sync="isScanMount" :close-on-click-modal="false" :close-on-press-escape="false" :before-close="beClose" class="scan_dialog">
      <ScanMount @scanCenterUpload="scanCenterUpload" :docId="mountFormId" @uploadSuccess="uploadSuccess" ref="scanList"></ScanMount>
    </el-dialog>
  </div>
</template>
<script>
import ScanMount from '@/components/mount/scan'
export default {
  name: 'EasyStarMount',
  data () {
    return {
      isActive: false,
      mountFormId: '',
      serachContent: '', // 搜索内容
      beforeData: {},
      file: '',
      fileData: [], // 原件列表
      multipleSelection: [],
      paramsObj: {},
      fullscreenLoading: false,
      isScanMount: false
    }
  },
  components: {
    ScanMount: ScanMount
  },
  mounted () {},
  methods: {
    scanCenterUpload (pdfFormData) {
      pdfFormData.append('docID', this.mountFormId)
      console.log(pdfFormData)
    },
    beClose (closeScanWindow) {
      this.$confirm('此操作将关闭该窗口, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        closeScanWindow(true)
      }).catch(() => {
        closeScanWindow(false)
      })
      // console.log(done)
    },
    windowResize () {
      document.getElementById('panel').style.height = window.innerHeight - 295 + 'px'
      if (this.isScanMount === true) {
        this.$refs['scanList'].windowResize()
      }
    },
    getCookie () {
      // 获取cookie字符串
      var strCookie = document.cookie
      // 将多cookie切割为多个名/值对
      var arrCookie = strCookie.split('; ')
      // 遍历cookie数组，处理每个cookie对
      for (var i = 0; i < arrCookie.length; i++) {
        var arr = arrCookie[i].split('=')
        // 找到名称为userId的cookie，并返回它的值
        if (arr[0] === 'JSESSIONID') {
          this.sessionId = arr[1]
          break
        }
      }
    },
    btnScanClick () {
      // console.log('id', this.mountFormId)
      if (navigator.userAgent.toLowerCase().indexOf('windows') >= 0) {
        // this.isScanMount = true
        this.getCookie()
        this.axios.post('http://127.0.0.1:51211/', {
          'command': 'start',
          'docId': this.mountFormId
        }, {
          headers: {
            'JSESSIONID': this.sessionId
          },
          withCredentials: true
        }).catch((res) => {
          console.log(res)
          alert(res)
          var scanWindow = window.open('_blank')// 先打开页面
          scanWindow.location = '/scan.html'// 后更改页面地址
        })
      } else {
        this.isScanMount = true
      }
    },
    openScan () {
      this.$refs['scanList'].init()
      // if (this.isScanMount === true) {
      //   this.$refs['scanList'].init()
      // }
    },
    closeScan () {
      this.$refs['scanList'].close()
    },
    uploadSuccess () {
      this.viewFile()
    },
    hide: function () {
      this.isActive = false
      this.fileData = []
      document.body.removeEventListener('click', this.hide, false)
    },
    show (showing) {
      this.isActive = showing
      // if (this.isActive) {
      //   // document.body.addEventListener('click', this.hide, false)
      //   document.getElementsByClassName('el-menu-item').addEventListener('click', this.hide, false)
      // } else {
      //   // document.body.removeEventListener('click', this.hide, false)
      //   document.getElementsByClassName('el-menu-item').addEventListener('click', this.hide, false)
      // }
    },
    // 新增原件
    uploadImg () {
      // console.log('参数', param)
      let formData = new FormData()
      formData.append('files', this.beforeData)
      formData.append('docID', this.mountFormId)
      this.axios.post(this.api + this.easyStarAPI.FileUploadAction, formData).then((res) => {
        console.log('新增结果', res)
        if (res.data.code === 0) {
          this.viewFile()
        }
      })
    },
    // 文件上传之前
    beforeAvatarUpload (file) {
      console.log('上传前', file)
      this.beforeData = file
    },
    // 删除原件
    deleteManyFile (item) {
      var arr = []
      arr.push(item.FILEID)
      const instance = this.axios.create({
        // withCredentials: true,
        headers: {'Content-Type': 'application/json'}
      })
      this.$confirm('此操作将永久删除该原件, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        instance.post(this.api + this.easyStarAPI.DelFile, {
          docID: this.mountFormId,
          fileID: arr
        }).then((res) => {
          if (res.data.code === 0) {
            this.viewFile()
            this.$message({
              type: 'success',
              message: '原件删除成功!'
            })
          } else {
            this.$message({
              type: 'error',
              message: res.data.errMsg
            })
          }
        }).catch((error) => {
          console.log(error)
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消原件删除'
        })
      })
    },
    accept (id) {
      this.mountFormId = id
      this.viewFile()
    },
    // 查看
    btnSearchFile () {
      this.viewFile()
    },
    viewFile () {
      this.fullscreenLoading = true
      this.axios.get(this.api + this.easyStarAPI.GetFiles1, {params: {
        docId: this.mountFormId
      }}).then((res) => {
        // console.log('原件数量', res)
        if (res.data.code === 0) {
          this.fullscreenLoading = false
          var newArr = res.data.list
          this.parseFile(newArr)
        }
      })
    },
    parseFile (col) {
      var newCols = []
      col.forEach(element => {
        var newCol = {}
        newCol.DESCRIOTIO = element.DESCRIOTIO
        newCol.FILEID = element.FILEID
        newCols.push(newCol)
      })
      this.fileData = newCols
    },
    // 查找该条目下原件
    searchOnlyFile () {
      this.axios.post(this.api + this.easyStarAPI.GetFiles1, {
        docID: this.mountFormId,
        fileName: this.serachContent
      }).then((res) => {
        // console.log('搜索结果', res)
        if (res.data.code === 0) {
          if (res.data.list.length !== 0) {
            this.fileData = res.data.list
          } else {}
        }
      })
    },
    // 查看该条目下某一个原件的pdf格式
    viewFilePDF (item) {
      this.getPdfFun(this.mountFormId, item.FILEID, 1, -1, item.DESCRIOTIO)
    },
    getPdfFun (docID, fileID, beginPage, endPage, DESCRIOTIO) {
      this.axios.post(this.api + this.easyStarAPI.GetFileEntityAction, {
        docID: docID,
        fileID: fileID,
        beginPage: beginPage,
        endPage: endPage
      }, {responseType: 'blob'}).then((res) => {
        let url = window.URL.createObjectURL(new Blob([res.data]))
        // let d = DESCRIOTIO
        var tempwindow = window.open('_blank')// 先打开页面
        // tempwindow.location = '/static/assets/pdf/web/viewer.html?title=' + d + '&file=' + url// 后更改页面地址
        tempwindow.location = '/static/assets/pdf/web/viewer.html?file=' + url// 后更改页面地址
      })
    }
  }
}
</script>
<style>
.mount_content {
  position: absolute;
  width:15%; top:210px;
  right:23px;
  background: #f4f9fa;
  z-index: 4;
  overflow-y:auto;
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
  box-shadow: -2px 0px 10px #ccc;
  font-family: '微软雅黑'
}
.btnBack {
  font-size: 1.5em;
  cursor: pointer;
  float: right;
  margin: 10px 10px 10px 0px;
  color: #999;
}
.btnRefresh {
  font-size: 1.5em;
  cursor: pointer;
  color: #999;
  margin: 10px 0px 10px 10px;
  float: left;
}
.mount_text {
  font-size: 1.2em;
  text-align: center;
  color: #409EFF;
  margin-bottom: 20px;
}
.btnScanning {
  color: #fff!important;
  float:right;
  margin-right: 5px;
  font-size: 1em;
  background-color:#409EFF!important;
  cursor: pointer;
}
.btnDelete {
  cursor: pointer;
  position: absolute;
  right: 5px;
  top: 16px;
}
.mount_message {
  cursor: pointer;
  width: 100%;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.collapse-wrap {
  transition: 0.25s linear;
}
.trans-enter-active {
  opacity: 0;
  transform: translate(100%, 0);
}
.trans-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
  transform: translate(100%, 0);
}
.upload-demo.docmain_upload {
  display: inline-block;
}
.searchRow{
  width: 90%;
  margin: 0 auto;
  margin-bottom: 20px;
  margin-top: 10px;
}
.searchRow .search {
  width:100%;
}
.search .el-input__icon {
  font-size: 1.5em;
}
.mountClose {
  font-size: 2em;
  color: rgb(0, 0, 0);
  cursor: pointer;
  position: absolute;
  top: 4px;
}
.moufile {
  position: relative;
}
.moufile_content {
  border-right: 1px solid #eee;
  border-bottom: 1px solid #eee;
  border-top: 1px solid #ffffff;
  border-left: 1px solid #ffffff;
  padding: 10px 0px;
  text-align: center;

}
.moufile_content:hover {
  border-color:#409EFF;
  box-shadow: 2px 2px 5px #bbb;
}
.mouadd {
  /* margin-top: 2px; */
  padding-left:0px;
  position: relative;
  /* border-right: 1px solid #eee;
  border-bottom: 1px solid #eee; */
}
.mouadd_content {
  border-right: 1px solid #eee;
  border-bottom: 1px solid #eee;
  border-top: 1px solid #ffffff;
  border-left: 1px solid #ffffff;
  padding: 10px 0px;
  text-align: center;
}
.circle {
  font-size: 2em!important;
  position: absolute;
  top: 16px;
  right: 5px;
}
.circle .el-progress-circle {
  width: 1.2em!important;
  height: 1.2em!important;
}
/* .scan_dialog .el-dialog {
  width: 650px!important;
  max-width: 650px!important;
} */
</style>



